/**** Module Info
{
  'name': 'Range Slider Widget Module',
  'summary': 'Single value slider that tests for proximity to some value',
  'keywords': ['continuum', 'slider', 'widget', 'simple slider'],
  'settings': [
    {
      'name': 'Widget Element Template (Global)',
      'path': ['widget', 'typeConfigs', 'rangeslider', 'frameTemplate'],
      'summary': 'Overall Underscore (ERB syntax) template for widget',
      'description': 'Include attribute data-cmpr-range-slider \
      to inject slider functionality. You may also include an \
      img[data-cmpr-preview-target] to inject preview area behavior.',
      'input': 'textarea',
      'validation': 'template'
    },
    {
      'name': 'Widget Element Template (By Field)',
      'path': ['widget', 'fieldConfigs', '~Name of Field~', 'frameTemplate'],
      'summary': 'Overall Underscore (ERB syntax) template for widget',
      'description': 'Does the same thing as the global flag for this widget, \
      but scoped to only one field type and overrides the global option.',
      'input': 'textarea',
      'validation': 'template',
      'optional': true
    },
    {
      'name': 'Range Bound Text Decorator',
      'path': ['widget', 'fieldConfigs', '~Name of Field~', 'decorator'],
      'summary': 'Function to decorate values on range bounds with units',
      'description': 'Callback takes a string or number and must return \
      a string. Typically used to add monetary or measurement units.',
      'input': 'textarea',
      'validation': 'function',
      'optional': true
    }
  ]
}
****/

define('widgets/rangeslider.widget',[], function() {
  // Prevents un-syncing of constant, also saves on space when minified
  var scaleTop = 1000;
  // Needs widgetController for state updates and notifications
  return function() {
    // Setting up some shorthand aliases
    var _ = $CMPR._, $ = $CMPR.$, self = this,
      // And storage cross-module
      options, $slider, $frame,
      // Channel used to show the slider was dragged at either end
      sliderUsed = $CMPR.log('Range Slider Used');
      function stateChange() {
        $CMPR.push('widget.stateChange');
      }
      // Returns real value of the bottom of the slider
      function rangeMin() {
        // Use passed value or default to min of 0
        return (options.values[0] || {value: 0}).value;
      }
      // Returns real value of the top of slider
      function rangeMax() {
        // Use passed value or default to value above
        return (options.values[1] || {value: scaleTop}).value;
      }
      // Returns the real top minus real bottom - real range
      function range() {
        // Simple math. This on on real, not scaled-to-1000 axis
        return rangeMax() - rangeMin();
      }
      // Converts a real value into a 1000 scale one used by slider widget
      function toThousand(val) {
        // Offset by the scale's bottom and multiply by range proportion
        return (val - rangeMin()) / range() * scaleTop;
      }
      // Converts a 1000-axis value from slider into real value to use elsewhere
      function fromThousand(val) {
        // Proportional conversion first, then add in offest from real base/min
        return val * (range() / scaleTop) + rangeMin();
      }
      // Returns the current real top and bottom values decorated by a callback.
      function genLabel() {
        // Left index is minimum, to is the range word, right index is maximum.
        return decorateVal(0) + ' To ' + decorateVal(1);
      }
      // Takes 0 or 1 and returns decorated real value, using option or identity
      function decorateVal(idx) {
        // Try using config, else do nothing, invoked on passed slider index
        return (options.decorator || _.identity)($slider.slider('values', idx));
      }


    /**
     * Create a DOM node for element and make element active.
     *
     * @param {Object} configs Widget configuration payload
     * @return {jQuery} DOM node representation of widget.
     */
    this.onCreate = function(configs) {
      // Save in the cross-function free variable
      options = configs;
      // Render whole widget using passed template and options
      $frame = $CMPR.render(options.frameTemplate, options);
      // Seek out the flagged element to attach slider functionality to
      $slider = $frame.find('[data-cmpr-range-slider]').slider({
        // Rather than continuum
        range: true,
        // Use function defined above to find the bottom
        min: rangeMin(),
        // Same for the top
        max: rangeMax(),
        // Smallest granularity possible (only takes integers)
        step: 1,
        // Values to set the handles to when initializing
        values: [
          // Only try accessing properties if defaultValue property is present
          (options.defaultValues ?
            options.defaultValues[0] : null) || rangeMin(),
          (options.defaultValues ?
            options.defaultValues[1] : null) || rangeMax()
        ],
        // Change event fired when sliding stops - notify of update and log
        change: function() {
          // Update thru state reads
          stateChange();
          // Log that slider was used with full widget state
          sliderUsed(self.state());
        },
        // More common event happens during slides, so throttle it
        slide: _.throttle(stateChange, 100)
      });
      // Reference to the full DOM node for the widget
      return $frame;
    };


    /**
     * Public handle to generate and read the criteria query fragment for widget
     *
     * @return {Object} Widget's criteria preference fragment
     */
    this.state = function() {
      return (function(low, high){
        return {
          // Use manual value since we need to be able to decorate ranges
          valueLabel: (options.labelBuilder ||
            genLabel)($frame, low, high, _.process(fromThousand, options.decorator) || _.identity),
          // Rangesliders always listen and produce same field once built
          field: options.listenTo,
          // Used when you need to check for intervaled numberic types
          operator: 'range',
          // Object with min and max for easy parsing into numeric intervals
          value: [low, high]
        };
      })(Math.floor(toThousand($slider.slider('values', 0))),
        // Ceiling to coerce towards one thousand, right-hand slider index
        Math.ceil(toThousand($slider.slider('values', 1))));
    };


    /**
     * Handle to push state into the instance of rangeslider.
     *
     * @param {Object} pref Criteria preference fragment.
     */
    this.onData = function(pref) {
      // Assign left handle to either the pref's min value or zero
      pref && pref.value && $slider.slider('values',
        _([+pref.value[0] || 0, +pref.value[1] || scaleTop])
        .map(fromThousand));
    };


    /**
     * Public handle to do widget cleanup from DOM and JS heap
     */
    this.onDestroy = function() {
      // Simple removal from DOM because no global exposure.
      $slider.remove();
    };
  };
});

