define('entry/namespace.setup',['../lib/underscore', '../lib/jquery', './underscore.additions',
  '../lib/jquery.isotope', '../lib/jquery-ui', '../lib/touch',
  '../lib/cookie', '../lib/pubsub'],
function(__, $$$, improveUnderscoreFor,
  addIsotopeTo, addUiTo, addTouchTo,
  addCookieTo, addHubTo) {
  return function(namespace) {
    (function(app) {
      // Sets context or param object to _ lib
      __(app);
      // Takes param._ and injects more functions into it
      improveUnderscoreFor(app);
      // Give the app object namespaced $ reference
      app.$ = $$$();
      // Takes a $ reference and adds jquery-ui to it
      addUiTo(app.$);
      // Takes a $ reference and adds isotope.js to it
      addIsotopeTo(app.$);
      // Takes a $ reference and adds touchpunch to it
      addTouchTo(app.$);
      // Takes a $ reference and adds jquery.cookie.js to it
      addCookieTo(app.$);
      // Add message queueing hub to app namespace
      addHubTo(app);
      // Namespace function additions

      app.expose = function(api) {
        app._(api).map(function(data, handle) {
          app[handle] = data;
        });
        return api;
      };
    })(window[namespace] = {});
  };
});

