define('entry/cmpr.utilities',[], function() {
  return function(configs) {
    var _ = $CMPR._;
    // Expose more on $CMPR API
    $CMPR.expose({
      // Invocation to make jQuery objects from template and contect object
      render: function(template, value) {
        // Coerce to template if a string, render template and apply jQuery
        return $CMPR.$(_.isFunction(template) ?
          template(value) : _.template(template.replace(new RegExp(configs('assetPlaceholder'), 'g'),
            configs('assetUrl')), value || {}));
      }
    });
  };
});

