/**** Module Info
{
  'name': 'Widget Interface Wrapper Logic',
  'summary': 'File which parses declarative actions on widgets',
  'description': 'Handles data binding, interaction events, and feature \
  flagging on each of the wrappers around widgets, like flexibility, \
  hide/show, and value labeling.',
  'settings':[
    {
      'name': 'Widget Wrapper Element Template',
      'path': ['widget', 'uiLogic', 'wrapperTemplate'],
      'summary': 'Declarative template for data bindings and wrapper events',
      'description': 'String template with tons of magic bindings a la \
      Angular.js, allowing binding of widget internals to a specific \
      injection host by flagging with the "data-cmpr-inject-widget" attribute \
      , binding of events with this syntax: \
      data-cmpr-attach="eventName:action(params)", binding of data sync with \
      this syntax: data-cmpr-binding="binding(params)", and single pass \
      binding with data-cmpr-content="propertyName".',
      'input': 'textarea',
      'validation': 'template'
    },
    {
      'name': 'Interface Element Flagging (Global)',
      'path': ['widget', 'uiFlags'],
      'summary': 'Object of boolean flags for single feature',
      'description': 'The keys are the features being flagged, and the logic \
      of hide versus show depends on the flagging declarations in the widgets.',
      'input': 'textarea',
      'validation': 'object'
    },
    {
      'name': 'Interface Element Flagging (By Type)',
      'path': ['widget', 'typeConfigs', '~Type of Widget~', 'uiFlags'],
      'summary': 'Object of boolean flags for single feature',
      'description': 'The keys are the features being flagged, and the logic \
      of hide versus show depends on the flagging declarations in the widgets. \
      Overrides the global setting for Element Flagging and only applies \
      to the selected type',
      'input': 'textarea',
      'validation': 'object',
      'optional': true
    },
    {
      'name': 'Interface Element Flagging (By Field)',
      'path': ['widget', 'fieldConfigs', '~Name of Field~', 'uiFlags'],
      'summary': 'Object of boolean flags for single feature',
      'description': 'The keys are the features being flagged, and the logic \
      of hide versus show depends on the flagging declarations in the widgets. \
      Overrides the global and widget-type level settings for Elemen Flagging \
      and only applies to the specific field of widget.',
      'input': 'textarea',
      'validation': 'object',
      'optional': true
    },
    {
      'name': 'Custom Wrapper Data Bindings',
      'path': ['widget', 'uiLogic', 'bindings'],
      'summary': 'Object storing names callbacks that update with controller',
      'description': 'The keys on this object denote the names that are given \
      to the bindings, and the values are the callbacks themselves. This \
      setting is used to provide the Widget "Wrapper Element Template" with \
      any custom functions needed. These functions are meant to update a piece \
      of the DOM to make sure data is displayed correctly.',
      'input': 'textarea',
      'validaion': 'object',
      'optional': true
    },
    {
      'name': 'Custom Wrapper Action Bindings',
      'path': ['widget', 'uiLogic', 'actions'],
      'summary': 'Used to create custom event listners on pieces of wrapper.',
      'description': 'The keys on this object denote the names that are given \
      to the callbacks executed when bound events to the wrapper are fired. \
      All of these functions can be event-independent, that is, you can assign \
      an action here in the object and not care about if it will be triggered \
      off of a "click" or "mouseenter" event. This setting if for addding \
      custom functions in addition to the handful of built in ones: ',
      'input': 'textarea',
      'validation': 'object',
      'optional': true
    }
  ]
}
****/

define('widgets/widget.uilogic',[], function() {
  return function(allConfigs) {
    var _ = $CMPR._, $ = $CMPR.$, bindings, actions,
      configs = allConfigs('widgetWrapper');

    // Add functions to API
    $CMPR.expose({
      /**
       * Force everything closed based on injection internals
       */
      closeAllWidgets: function() {
        // This is the injection special attribute for internals
        toggle($('[data-cmpr-inject-widget]'), 'hide');
      },
      /**
       * Hide an element when you click outside of a region
       * Also builds reference to event cleaner function
       *
       * @param {jQuery} hideable Element, {jQuery} region Boundary for click
       * @return {function} To clean click binding
       */
      clickOutBinding: function(hideable, region, event) {
        // Self-execute to alias event callback
        return (function(binding) {
          // Attach to whole page to get the outer regions
          $(document).on('mouseup', binding);
          // Return the cleaning function
          return function() {
            // Which simply unbinds from document
            return !!$(document).unbind('mouseup', binding);
          };
        // Alias the callback
        })(function(e) {
          // Check is the outer region is the region itself or a child
            if (!region.is(e.target) && !region.has(e.target).length)
              // and hide.
              toggle(hideable, 'hide');
        });
      }
    });

    
    if (!configs)
      return;

    $CMPR.on('widget.mixins', function(pack) {
      decorate(pack.widget, pack.internals, pack.payload);
    });

    function toggle($obj, direction) {
      $obj[direction || 'toggle']();
      $obj.parent().removeClass('cmpr_state_opened cmpr_state_closed');
      $obj.parent().addClass('cmpr_state_' + (($obj.is(':visible') || direction === 'show') ? 'opened' : 'closed'));
    }


    // Built in functions for binding data to widget wrappers
    bindings = _({
      /**
       * Default method to build a value label.
       * No state: Any
       * Value label defined: that value
       * State with no value: Any
       * Else build the ___, ____, and _ More statement.
       */
      valueLabel: function(state, opts, element) {
        // self-executing because we need the conditions and I hate switch case
        element.html((function() {
          // If there's not a state,
          if (state == null)
            // ... default to unconstrained
            return 'Any';
          // If there's a manual value label
          if (state.valueLabel)
            // Use that
            return state.valueLabel;
          // If there's a state but no value,
          if (state.value == null)
            // Also unconstrained
            return 'Any';
          // If the state is NOT an array,
          if (!_.isArray(state.value))
            // Use string value
            return state.value;
          // If there's a flag for using numbers only
          if (opts && opts.numberOnly)
            // Just use the length
            return state.value.length;
          // Slice down by two and do the _ More statement otherwise
          return state.value.length <= 2 ?
            state.value.join(', ') :
            state.value.slice(0, 2).join(', ') + ' and ' +
              (state.value.length - 2) + ' More';
        })());
      },
      /**
       * Default method to bind state on read.
       */
      toggleFlexibility: function(state, opts, element, type, klass) {
        // if no state to set flex on
        if (!state)
          // kill function
          return;
        // Copy known flexiblity from closure into state
        state.mustHave = opts.mustHave;
        // Remove all actives
        element.removeClass('cmpr-soft-active cmpr-hard-active ' +
          (klass || ''));
        // Add active back in depending on flexibility
        element.addClass('cmpr-' + type + 'active' +
          (((state.mustHave ? 'hard' : 'soft') === type) ? (' ' + klass) : ''));
      }
    // Make bindings extensible by merging object from configs
    }).extend(configs.customBindings || {}),


    // Built in functions for binding DOM events to widget wrappers
    actions = _({
      /**
       * Toggle visibility of widget
       *
       * @param {Object} options, {Object} widget
       */
      toggleWidget: function(opts, widget, event) {
        var toggleRegion = widget.$$element.find('.cmpr-injected-widget');
        if (widget.$$element.has(event.target).length &&
          !toggleRegion.has(event.target).length)
          toggle(toggleRegion);
      },
      /**
       * Toggle flexibility of widget
       *
       * @param {Object} options, {Object} widget, {string} type, {string} klass
       */
      toggleFlexibility: function(opts, widget, event, type, klass) {
        // If it's hard, use true
        opts.mustHave = type === 'hard';
        // Find the opposite and remove active flag
        widget.$$element.find('.cmpr-' + (opts.mustHave ? 'soft' : 'hard') +
          '-active').removeClass((klass || '') + ' cmpr-' +
          (opts.mustHave ? 'soft' : 'hard') + '-active');
        // Apply active classes to the clicked element
        $(this).addClass((klass || '') + ' cmpr-' + type + '-active');
        // Signal controller to update
        $CMPR.push('widget.stateChange');
      },
      /**
       * Make an element hide when you click outside of widget's internals
       *
       * @param {Object} options, {Object} widget
       * @return {function} Cleanup callback for event on window.document
       */
      clickOutHide: function(opts, widget, event) {
        // Find the internals injection point widget element itself
        return $CMPR.clickOutBinding(widget.$$element
          .find('[data-cmpr-inject-widget]'), widget.$$element);
      },
      /**
       * Kill a widget
       *
       * @param {Object} options, {Object} widget
       */
      close: function(opts, widget, event) {
        // Kill widget using controller action to keep states updated
        $CMPR.push('widgetController.destruction', widget);
      }
    // Make extensible from forgeConfigs by extending actions object
    }).extend(configs.customActions || {});


    /**
     * Adds the UI reserved class wrappers, field labels, dynamic state labels,
     *   and ui logic to any widget passed in without the customInjection flag.
     *
     * @param {Object} widget Closure, {Object} payload Options/context
     * @return {Object} Modified widget closure
     */
    function decorate(widget, internals, payload) {
      // Set up storage for cleaners and updaters, render widget internals
      var updaters = [], cleanableEvents = [];
        // Render element with pieces hidden based on flag object
      widget.$$element = (function(template) {
        // On each element flagged with injection attribute,
        _.chain(template.find('[data-cmpr-inject-widget]'))
          .map($).each(function(injectionTarget) {
          // Find the injection verb method
          injectionTarget[injectionTarget.attr('data-cmpr-inject-widget') ||
          // Use with the widget internals, add class to show it was injected
            'append'](internals)
              .addClass('cmpr-injected-widget');
        });
        // Return the rendered template
        return template;
      // Aliasing the template into a jQuery object 'template'
      })(function(tpl) {
        return $CMPR.render(tpl, {
          widget: widget,
          configs: payload
        }).attr('id','cmpr_' + payload.
          listenTo.replace(/\./g, '_') + '_widget');
      }(configs.templates[payload.wrapperTemplate || 'main']));


      // On the widget, find all the feature flags
      _.chain(widget.$$element.find('[data-cmpr-flag]'))
        .map($).each(function(elem) {
        // Complex condition, self-executing function
        if ((function(flagValue) {
          // No flag at all, if the flag has hide: and the flag is true,
          //   or if no hide: and flag is false
          return !flagValue || (~flagValue.indexOf('hide:') ?
            payload.wrapperFlags[flagValue.replace('hide:', '')] :
            !payload.wrapperFlags[flagValue]);
          // alias the flag string
        })(elem.attr('data-cmpr-flag')))
          // Hide if condition is met
          elem.hide();
      });


      // Single-write static content binder function, find each binding flag
      _.chain(widget.$$element.find('[data-cmpr-content]'))
        .map($).each(function(contentBindable) {
        // And inject the value from the payload context into the flag
        contentBindable.text(payload[contentBindable
          .attr('data-cmpr-content')]);
      });


      // Build state updated bindings - take element with the attribute
      updaters = _.chain(widget.$$element.find('[data-cmpr-binding]'))
        .map($).map(function(bindable) {
        // Run function with aliased invocation string
        return (function(boundInvocation) {
          // Run function with aliased function name and its arguments
          return (function(boundFuncName, params) {
            // Return function that takes state and option context
            return function(state, opts) {
              // And runs function with context and args from template
              _.partial(bindings[boundFuncName], state, opts, bindable)
                .apply(null, params);
            };
          // Aliasing the function name and parameters
          })(boundInvocation[0],
            _(boundInvocation.slice(1).join('').replace(')', '')
              .split(',')).map(eval));
        // Alias to the text on the binding
        })(bindable.attr('data-cmpr-binding').split('('));
      });


      // build actions on everything in the template with the flag
      _.chain(widget.$$element.find('[data-cmpr-attach]')
        .andSelf()).map($).map(function(attachableItem) {
        // Alias for value of the attach property
        (function(attachmentInvocation) {
          // Only run if valid invocation
          if (attachmentInvocation)
            // Aliases for event name(s) and bound invocation string
            (function(eventTrigger, invocation) {
              // Aliases for the function name and its extra param(s)
              (function(funcName, rest) {
                // Special case for non-native event
                if (eventTrigger === 'special')
                  // Assume special needs special cleaning/jQuery can't handle
                  cleanableEvents.push(actions[funcName]
                    .apply(attachableItem, [payload, widget, {}].concat(rest)));
                else
                  // Attach events using jQuery
                  attachableItem.on(eventTrigger, function(ev) {
                    // Action name has the context and any extra params
                    actions[funcName]
                      .apply(attachableItem, [payload, widget, ev].concat(rest));
                  });
              // Aliasing for the function name and the extra parameters
              })(invocation[0], _(invocation.slice(1).join('')
                .replace(')', '').split(',')).map(eval));
            // Aliasing for the event names and bound function text
            })(attachmentInvocation.split(':')[0],
              attachmentInvocation.split(':').slice(1).join('').split('('));
        // Alias the value of the property
        })(attachableItem.attr('data-cmpr-attach'));
      });

      if (!$CMPR.running || payload.forceClosed)
        toggle(widget.$$element.find('[data-cmpr-inject-widget]'), 'hide');
      else
        toggle(widget.$$element.find('[data-cmpr-inject-widget]'), 'show');


      // Inject extra step wherein we clean any special bindings and
      //   remove wrapper we just built...
      widget.onDestroy = _.isFunction(widget.onDestroy) ?
      _.wrap(widget.onDestroy, function(oldDestroy) {
        // Store for wrapper destroy
        var elementRef = widget.$$element;
        // Invoke all the cleaners to unbind specials and ready for GC
        _(cleanableEvents).invoke('call', null);
        // Original destroy function
        oldDestroy(widget);
        // Use stored reference to remove the rest of the content
        elementRef.remove();
      // In case of not having onDestroy function, do nothing to it
      }) : widget.onDestroy;

      // Wrap state function to trigger bindings on state reads (updates)
      widget.state = _.isFunction(widget.state) ?
      _.wrap(widget.state, function(orig) {
        // Grab the state only once
        var state = orig();
        // Use as the state when invoking all binders, with option context
        _(updaters).invoke('call', null, state, payload);
        // Return state after invoking all the binder mutations
        return state;
      // If the state wasn't a function we could hijack, don't modifiy it.
      }) : widget.state;
    };
  };
});

