/**** Module Info
{
  'name': 'No Matches Found Messaging Mixin',
  'summary': 'Runs tests against math % and raw scores, then displays messages',
  'keywords': ['no match', 'no results'],
  'optional': true,
  'settings': [
    {
      'name': 'Message Template',
      'path': ['gridwall', 'mixins', 'noResults', 'messageTemplate'],
      'summary': 'Underscore/ERB format template to inject case objects into.',
      'decription': 'Uses Underscore format to make a DOM fragment in which \
      to inject message resolution object when the associated condition is met',
      'input': 'textarea',
      'validation': 'template'
    },
    {
      'name': 'Cases',
      'path': ['gridwall', 'mixins', 'noResults', 'cases'],
      'summary': 'Condition/message pair objects for No Results logic',
      'description': 'Each object should have condition and result properties.',
      'array': true,
      'complex': true,
      'each': [
        {
          'property': 'condition',
          'description': 'Callback that takes an array of objects with \
          matchPercentage and score properties corresponding to the solutions \
          that are about to be rendered, and returns true or false to indicate \
          some condition is met.',
          'input': 'textarea',
          'validation': 'function'
        },
        {
          'property': 'result',
          'description': 'Object that stores strings or context consumed by \
          "Message Template" for this module, that is rendered when the \
          "condition" paired with this result is met.',
          'input': 'object',
          'validation': 'object'
        }
      ]
    }
  ]
}
****/

define('gridwall/render/noresults.mixin',[], function() {
  return function(configs) {
    // Alias libs and setup the storage for the message's jQuery object node
    var _ = $CMPR._, $ = $CMPR.$, $msgTarget = $('<span>'), cases, template,
      // Simple process to grab only relevant data for matching conditions
      matchesAndScores = _.mapBy(_.process(
        _.get('data'), _.picker('score', 'matchPercentage')));

    if (configs('noResults')){
      $CMPR.on('transport.data.received.first', function() {
        _.defer($CMPR.on, 'gridwall.update.mixin', function(pack) {
          showMessageFor(pack.solutions, pack.gridwall);
        });
      });
      cases = configs('noResults', 'cases');
      template = configs('noResults', 'messageTemplate');
    }

    /**
     * Conditionally display messages if there is a met condition callback
     *   on the score and/or matchPercentage properties of the solution data
     *
     * @param {Array} solutions, {jQuery} gridwall
     */
    function showMessageFor(solutions, gridwall) {
      // Aliasing metCase since it's a complex process, only want to do one time
      (function(metCase) {
        // Should any of the functions match
        if (metCase) {
          // Remove the old messaging
          $msgTarget.remove();
          // Replace reference with element using template, case result metadata
          $msgTarget = $CMPR.render(template, metCase.result);
          // Insert it above/before the gridwall node passed in
          $msgTarget.insertBefore(gridwall);
        // In case no special cases are met
        } else
          // Hide the field so as not to confuse users
          $msgTarget.hide();
      // Grab all the cases or empty array
      // Find a case condition function that returns true, using:
      //   ... the simplified array of incoming solution data.
      })(_(cases || [])
        .find(_.functional('condition')(matchesAndScores(solutions.incoming))));
    };
  };
});

