/**** Module Info
{
  'name': 'More Like This Mixin Module',
  'summary': 'Makes flagged elements in solution run an adjacency search',
  'keywords': ['more like this', 'mlt', 'similar', 'others', 'matching items'],
  'settings': [
    {
      'name': 'MLT Shown Widget Field Whitelist',
      'path': ['gridwall', 'solution', 'mlt', 'whitelist'],
      'summary': 'Array of fields that will show when MLT is used',
      'description': 'While all of the fields on the solutions will be \
      searched against, only the field names in this list will show.',
      'array': true,
      'input': 'text',
      'validation': 'string'
    }
  ]
}
****/

define('gridwall/solutions/mlt.mixin',[], function() {
  return function(configs) {
    if (configs('moreLikeThis')){
      $CMPR.on('solution.mixins', $CMPR._.mapBy(mixin));
      $CMPR.on('gridwall.prerender', $CMPR._.mapBy(function(solution) {
          if (solution && solution.element){
            solution.element.removeClass(mltClass);
            if (solution === bound){
              solution.element
                .attr('data-es-score', '1000').addClass(mltClass);
              bound = null;
            }
          }
      }));
    }

    // Alias underscore and set up a log channel for module event use
    var _ = $CMPR._, logMLT = $CMPR.log('MLT'), bound,
    // Soon: allow runtime override of the selector used for binding MLT clicks
    mltClass = 'cmpr_mlt_selection';

    /**
     * Binds a click event to anything tagged with a special class,
     *   which makes the app query for items similar to the clicked solution
     *
     * @param {Object} solution With element and data properties
     * @return {Object} Solution modified to have the special MLT events
     */
    function mixin(solution) {
      // Setting up storage for preferences derived from clicked base object
      var prefs;
      // Don't try doing anything if there's not an element to bind to
      if (!solution.element)
        return;
      // Find the injection points for the MLT configs, bind on click
      solution.element.find('.cmpr_mlt_trigger').on('click', function() {
      bound = solution;
      // Log that MLT was clicked, passing in clicked solution information
      logMLT(solution.data);

      // Start building prefs, beginning with the compare data on solution
      prefs = _.chain(solution.data.compare).map(function(val, key) {
          return {
            // Resolving path from JS friendly to ES friendly
            field: 'compare.' + key,
            // Compare fields are always tokenized arrays!
            value: val[0],
            // And try doing exact matches to the numerics
            operator: '=='
          };
        // Compare fields are interesting in that a -1 denotes unused field.
        }).reject(_.process(_.get('value'), _.equals(-1))).value()
        // In addition to compare fields, we also use the display strings
        .concat(_.chain(solution.data.display).map(function(val, key) {
          return {
            // Again stranslating property path
            field: 'display.' + key,
            // This time we don't need to dereference first in array, because
            value: val,
            // We are using a many-many matcher instead of exact equivalence
            operator: 'any'
          };
        // Break the underscore chain we've been tapping
        }).value());
        // Use the prefs we just generated to build the LOUD widgets
        $CMPR.push('widgetController.forcedState', _(prefs)
          .filter(function(pref) {
            // based on meeting the whitelist from configs
            return _(configs('moreLikeThis', 'visibleStates')).find(function(white) {
              // Seeing if the field matches the whitelisted terms
              return pref.field === white && (pref.forceClosed = true);
            });
          }));
        // Then silent async build rest to make hits higher quality invisibly
        $CMPR.emit('widgetController.forcedState', {
          prefs: prefs.concat($CMPR.getPreferenceState().findWhere({field: 'page.size'}).value() || []),
          silent: true
        });
      });
    };
  };
});

