/**** Module Info
{
  'name': 'Logger',
  'summary': 'Used to dynamically collect events and ship off to endpoint',
  'keywords': ['logs', 'analytics', 'events'],
  'settings': [
    {
      'name': 'Third Party Analytics Plugins',
      'path': ['logging', 'thirdPartyTargets'],
      'summary': 'Callbacks to fork all log payload arrays into when flushing',
      'description': 'Should be either excluded entirely or an array of \
      callbacks to fork data running through the flusher. Used for things \
      like adapters for Omniture and Google Analytics.',
      'input': 'textarea',
      'validation': 'function',
      'array': true,
      'optional': true
    }
  ]
}
****/


define('analytics/logger',[], function() {
  return function(logConfigs) {
    //// These are all of the public handles
    this.eventStorage = [];
    this.log = logEvent;
    //// End public handles

    // Alias some libraries
    var
      stringify = JSON.stringify,
      lastFlush = 0,
      // Bank of base64 charaters to use in random, near-unique id generation
      base64Chars = 'ABCDEFGHIJKLMNOPQRSTUVWXYZ' +
        'abcdefghijklmnopqrstuvwxyz0123456789+/',
      // cookie polyfill
      cookie = {
        get: function(id) {
          var hash;
          document.cookie.replace(new RegExp('cmpr-'+id+'=([^;]+)'),
            function(fullStr, match) {
              hash = match;
            });
          return hash || (function(size) {
            var base = [];
            while (size--)
              base.push(base64Chars[Math.floor(Math.random() * 64)]);
            return base.join('');
          })(32);
        },
        set: function(id, hash, expires) {
          document.cookie = ['cmpr-'+id+'=', hash, ';expires=',
            (expires || new Date(Date.now()+365*24*3600000))].join('');
        }
      },
      once = function(callback) {
        var done;
        return function(param) {
          done || callback(param);
          done = 1;
        };
      },
      // Set up private queue for unsent events 
      eventQueue = [],
      // Store URL for logging backend
      loggingTarget = loggingTarget = [location.protocol, '//', logConfigs.url].join(''),
      // Set bool flag for if we use a debug dumper to console
      isDebugging = /debug=true/.test(location.href),
      // Allow storage for third party analytics API adapter function(s)
      thirdPartyTargets = function(events) {
        var eventSize = events.length,
          targetSize = (logConfigs.thirdPartyTargets || []).length,
          ii = 0, jj;
        for (; ii < eventSize; ii++)
          for (jj = 0; jj < targetSize; jj++)
            logConfigs.thirdPartyTargets[jj](events[ii]);
      },
      // flushQueue method is debounced event sender
      flushQueue = function(){
        var now = new Date().getTime();
        if (lastFlush + 250 < now){
          sendEvents();
          lastFlush = now;
        }
      };
      // Set the $CMPR.uid
      cookie.set('uid', $CMPR.uid = cookie.get('uid'));
      // Set the $CMPR.sessid either based on the existing session or a 32 char one
      cookie.set('sessid', $CMPR.sessid = cookie.get('sessid'), '0');

    function appendGif(logBuffer, i) {
      for (i = 0; i < logBuffer.length; i++) {
        if (stringify(logBuffer.slice(0, i+1)).length > 2000)
          break;
      }
      if (i === 0)
        return false;
      var body = document.getElementsByTagName('body')[0];
      img = document.createElement('img');
      img.src = loggingTarget + '?payload=' + stringify(logBuffer.splice(0, i));
      img.style.display = 'none';
      body.insertBefore(img, body.firstChild);
      logBuffer.length && appendGif(logBuffer);
      img.parentNode.removeChild(img);
    }

    function sendEvents() {
      // Clone array of events in queue
      var payload = eventQueue.slice(0);
      // Empty the queue
      eventQueue = [];
      // If there's a location to try flushing to...
      if (loggingTarget)
        // Recursive call to send off the logs
        appendGif(payload);
      else
        // If no target, store events local, inspectable at $CMPR.logs()
        this.eventStorage = this.eventStorage.concat(payload);
      // In any case, try hitting the third parties with our payload
      setTimeout(function(){
        thirdPartyTargets(payload);
      }, 0);
    }


    /**
     * Builds log channels which store data from log events into... channels
     *
     * @param {String} eventName [, {function} decoration]
     * @return {function} Params passed and decorated if decorator included
     */
    function log(eventName, decoration) {
      // Bind logger to this Logger instance scope
      return function(data) {
        // Build decorated object out of data, coercing to array
        var decorated = (function(datum) {
          var logData = {
            // references free variable channel name from generator param
            event: eventName,
            // Stamp with UNIX epoch time in milliseconds
            time: new Date().getTime(),
            // Use analytics session id
            cmprSessionId: $CMPR.sessid || 'No session',
            // Use cookie id we grabbed
            userId: $CMPR.uid || 'No user ID',
            // Decorate data or pass over it,  empty object if no data
            data: (decoration || function() {
              return arguments.length > 1 ? [].slice.call(arguments, 0) : arguments[0];
            })(datum) || {}
          };
          return logData;
        })(data);
        // Check the debug flag we got from GET param regex /debug=true/
        isDebugging && window.console && console.log && console.log(decorated);
        // Add all the events into the queue we periodically flush
        eventQueue = eventQueue.concat(decorated);
        // Try flushing the queue (may be deferred if a lot is happening)
        flushQueue();
        // Return data back so you can chain off curried calls in normal code
        return data;
      // Our binding to the log closure's scope
      };
    }

    function logEvent(eventName, decoration) {
      return log(eventName, decoration);
    }

    window.$CMPRSANITIZE$ = function() {
      for(var key in window)
        if (/^\$CMPR.*\$$/.test(key))
          delete window[key];
    };

  };
});

