define('entry/encoding',[], function() {
  return function() {
    var _ = $CMPR._,
      fragmentDelimiter = '~~', equalDelemiter = '__',
      arrayDelimiter = '..', flag = '!';
    return $CMPR.expose({
      // Setup process to grab app state and encode to short string
      encodeState: function(state) {
        return _.chain(state).compact().map(function(elem) {
          var obj = {};
          obj.value = _.isArray(elem.value) ?
            (_(elem.value).map(function(single) {
              return _.isObject(single) ?
                JSON.stringify(single) :
                single.toString();
            }).join(arrayDelimiter) + flag) :
              _.isObject(elem.value) ? JSON.stringify(elem.value) : elem.value + '';
          obj.field = elem.mustHave ? (flag + elem.field) : elem.field;
          return [obj.field, obj.value].join(equalDelemiter);
        }).value().join(fragmentDelimiter);
      },
      decodeState: function(str) {
        return str ? _(str.split(fragmentDelimiter)).map(function(fragment) {
          return (function(field, value) {
            var obj = {};
            if (field && field[0] === flag) {
              obj.mustHave = true;
              obj.field = field.replace(RegExp('^' + flag), '');
            }else
              obj.field = field;
            if (value && value[value.length - 1] === flag)
              obj.value = _(value.replace(RegExp(flag + '$'), '')
                .split(arrayDelimiter))
                .map(function(single) {
                  return single[0] === '{' ? JSON.parse(single) : single;
                });
            else
              obj.value = value[0] === '{' ? JSON.parse(value) : value;
            return obj;
          }).apply(null, fragment.split(equalDelemiter));
        }) : [];
      }
    });
  };
});

