define('entry/compile.configs',[], function() {
  return function(forgeData) {
    var _ = $CMPR._;
    $CMPR.expose({
      // Object storing all URL params
      urlOverrides: _(location.href).run(
        // Regex to find get params
        _.match(/[\?|=](.*=.*)(\&|#)?/),
        _.condition(_.pass, _.process(
          _.get(1), _.split('&'), _.mapBy(_.split('=')), _.object),
        // if not param blob, return empty object
        _.fallTo({})))
    });
    _($CMPR.configFlags).run(
      // Mapping each key as a path and value as value
      _.mapBy(function(val, conf) {
        // return object to consume below
        return {path: conf.split(':'), val: val};
      }),
      // Filter generated array of path objects
      _.filterBy(function(conf) {
        // Using only ones pathed to forge as root
        return conf.path[0] === 'forge';
      // Make them underscore objects too
      }),
      _.mapBy(function(conf) {
      // Dig down into the forge object and set path item
      _.get(conf.path.slice(1,
        conf.path.length - 1))(forgeData)[_(conf.path)
          .last()] = JSON.parse(conf.val || '""');
    }));

    // Unpack compressed values on each of the forge's widget properties
    _(forgeData.widget.fieldConfigs).each(function(payload) {
      // Test to see if it's a shorthand values array for the field
      if (_.isArray(payload.values) &&
        (_.isString(payload.values[0]) || _.isNumber(payload.values[0])))
        // If it is, set to expanded form
        payload.values = _(payload.values).map(function(flatToken) {
          // By just making the token a value property on a new object
          return {value: flatToken};
        });
    });

    forgeData.assetUrl = $CMPRASSETURL$;
    (function(pattern, url) {
      forgeData.cssFile = forgeData.cssFile.replace(pattern, url);
      _(forgeData.widget.fieldConfigs).each(function(config) {
        if (_.isArray(config.values))
          _(config.values).each(function(value) {
            if (value && _.isString(value.url))
              value.url = value.url.replace(pattern, url);
          });
      });
    })(new RegExp(forgeData.assetPlaceholder, 'g'), $CMPRASSETURL$);

    forgeData.logging = (forgeData.logging || {});
    forgeData.logging.url = $CMPRLOGGINGURL$;
    forgeData.backend.url = $CMPRBACKENDURL$;
  };
});

