/**** Module Info
{
  'name': 'Isotope Render Strategy',
  'summary': 'Uses Isotope.js jQuery addon to do product render animations.',
  'keywords': ['isotope', 'render', 'animations', 'gridwall', 'solutions'],
  'settings': []
}
****/


define('gridwall/render/isotope.render',[], function() {
  // Setup with configs coming in from forge.
  return function(configs) {
    // Alias libraries we will be using
    var _ = $CMPR._, $ = $CMPR.$,
      products = configs('gridwall', 'productSelector'),
      gridwall = configs('gridwall', 'selector');



    /**
     * Starts isotope on attachment/page entry. Only callable once.
     *
     * @param {object} solutions Original solution object to initialize with
     * @return {this} Reference to initialized isotope render module
     */
    init = _.once(function(solutions) {
      // Set a reference for solution jQuery objects to reuse
      var $items = $(configs('gridwall', 'productSelector'));
      // Use container setting to attach elements
      $(gridwall).append(
        // Using only new element's 'element' (instead of data)
        _.chain(solutions.new).pluck('element')
        // Only use valid DOM elements and break chain
        .filter(_.or(_.isA($), _.isElement)).value());
      // Return reference to renderer for chaining possiblities
      return this;
    });


    /**
     * (Re-)renders the gridwall using an incoming solution object.
     * .render is public member name for gridwall DOM update strategies
     *
     * @param {Object} solutionObject With new, incoming, and old arrays
     */
    $CMPR.on('gridwall.render.isotope', function(solutionObject) {
      // Safe to always try because we use _.once above
      init(solutionObject);
      // Grab container and insert only INCOMING items
      $(gridwall).isotope({
          // The selector to use when finding rendered solutions
          itemSelector: products,
          // Prefer CSS3 then jQuery.animate then normal append/remove
          animationEngine: 'best-available',
          // Masonry allows for non-uniform tile sizes to be auto-arranged
          layoutMode: 'masonry',
          // Set column width of tiles for transform offset
          masonry: { columnWidth: configs('gridwall', 'tileWidth') },
          //  Bank of possible sort strategies
          getSortData: {
            // Our only metric is the data-es-score we get from ES
            rank: function($items) {
              // Grab the proper attribute and coerce to float/number
              return parseFloat($items.attr('data-es-score'));
            }
          }
        }).isotope('insert', $(_.chain(solutionObject.new)
        // Run mixins, get the DOM nodes, pluck first, and break chain
        .pluck('element').pluck(0).value()));

      // Run isotope on the newly merged and scored set
      $(gridwall).isotope('updateSortData', $(products))
        // Only show the current hits (class we added)
        .isotope({filter: '.cmpr-current-hit'})
        // Sort opration
        .isotope({
          // Using only strategy
          sortBy: 'rank',
          // And ensuring highets scores come first.
          sortAscending: false
        });
    });
  };
});

