define('analytics/console.polyfill',[], function() {
  return function() {
    // Polyflls missing methods on console for any device
    // Bucket to inspect any calls to missing methods
    $CMPR.debugPolyfill = [];
    // Iterate on methods from Chrome
    $CMPR._(['assert', 'clear', 'count', 'debug', 'dir', 'dirxml', 'error',
        'exception', 'group', 'groupCollapsed', 'groupEnd', 'info', 'log',
        'markTimeline', 'profile', 'profileEnd', 'table', 'time',
        'timeEnd', 'timeStamp', 'trace', 'warn'
    ]).each(function(method) {
      // Only stub undefined methods, else take original
      console[method] = console[method] || (function(callback) {
        // Make it a new method
        return function() {
          // Calling on a string starting with method name
          callback(['console.' + method + ' called:']
          // Then objects for each arguments
            .concat($CMPR._([].slice.call(arguments)).map(stringify))
          // Then add explanation
            .concat(['(You\'re seeing this message because the native ',
              'method does not exist in this browser.)']).join('\n'));
        };
      // Depending on the debug flag,
      })(/debug=true/.test(location.href) ?
        window.alert : $CMPR.debugPolyfill.push,
        (window.console = window.console || {}));
    });
  };
});
