/**** Module Info
{
  'name': 'Singleselect Widget Type',
  'summary': 'Widget that acts like an HTML select or set of radio buttons.',
  'keywords': ['select', 'radio', 'singleselect', 'widget', 'query builder'],
  'settings': [
    {
      'name': 'Main Widget Element Template (Global)',
      'path': ['widget', 'typeConfigs', 'singleselect', 'frameTemplate'],
      'summary': 'Overall Underscore (ERB syntax) template for widget',
      'description': 'Make sure to include an attribute for data-cmpr-repeater \
      to inject all of the value elements into. You may also include an \
      img[data-cmpr-preview-target] to inject preview area behavior.',
      'input': 'textarea',
      'validation': 'template'
    },
    {
      'name': 'Main Widget Element Template (By Field)',
      'path': ['widget', 'fieldConfigs', '~Name of Field~', 'frameTemplate'],
      'summary': 'Overall Underscore (ERB syntax) template for widget',
      'description': 'Does the same thing as the global flag for this widget, \
      but scoped to only one field type and overrides the global option.',
      'input': 'textarea',
      'validation': 'template',
      'optional': true
    },
    {
      'name': 'Repeated Value Element Template (Global)',
      'path': ['widget', 'typeConfigs', 'singleselect', 'valueTemplate'],
      'summary': 'The template to use for repeated values in widget',
      'description': 'Template is passed an object with context that has \
      value property, and maybe displayName and exampleUrl properties.',
      'input': 'textarea',
      'validation': 'template'
    },
    {
      'name': 'Repeated Value Element Template (By Field)',
      'path': ['widget', 'fieldConfigs', '~Name of Field~', 'valueTemplate'],
      'summary': 'Overall Underscore (ERB syntax) template for widget',
      'description': 'Does the same thing as the global flag for this widget, \
      but scoped to only one field type and overrides the global option.',
      'input': 'textarea',
      'validation': 'template',
      'optional': true
    },
  ]
}
****/




define('widgets/singleselect.widget',[], function() {
  // Reserver classes and selectors for this type of widget.
  var repeaterQuery = '[data-cmpr-repeater]',
    checkedClass = 'cmpr-checked-single-select',
    optionClass = 'cmpr-single-select-option';
  return function() {
    // Alias libs, set scope on vars we need in all methods
    var _ = $CMPR._, $ = $CMPR.$, options, $frame;
      /**
       * Activates the element you want to bind the active flag to.
       * Also notifies widgetController of updates.
       *
       * @param {jQuery} elem Element to assign activity to
       */
      function setValue(elem) {
        // Set closure's active value to the clicked one's
        options.activeValue = $(elem).data('valueObj');
        // Find all the singleselect options and deselect
        $frame.find('.' + optionClass).removeClass(checkedClass);
        // Re-select the clicked one
        $(elem).addClass(checkedClass);
        // onSelect function array defined in passed options, pass context
        _(options.onSelect || [])
          .invoke('call', null,
            $(elem).data('valueObj'), $(elem));
        // Finally, notify of update
        $CMPR.emit('widget.stateChange');
      }

      /**
       * Takes an array of values, renders them,
       *   adds click listeners, and returns elems.
       *
       * @param {Array} vals Like: {value:string [,valueObject:string]}
       * @return {Array} Of render jquery items for repeater
       */
      function renderValues(vals) {
        // Map the array and return the collection of elements
        return _(vals).map(function(valueObj) {
          // Use render function to build off passed template...
          return $CMPR.render(options.valueTemplate, {
              // ... and extra context of current value
              value: valueObj, options: options
            // Add in the reserved class that marks a singleselect option
            }).addClass(optionClass).data({valueObj: valueObj})
            // Add click event to set value of widget
            .on('click', function() {
              setValue(this);
            });
        });
      }

      /**
       * Checks for a value binder function and
       *   builds either static or recalculated possible options.
       */
      function valueUpdate() {
        // Keyword for function binder to produce values
        if (options.valueBinding)
          // Simply set
          options.values = options.valueBinding();
        // Re-render update into repeater
        $frame.find(repeaterQuery).empty().append(renderValues(options.values));
      }

    /**
     * Handle to create a widget's DOM representation from a config payload.
     *
     * @param {Object} configs See docs for format
     * @return {jQuery} Singleselect widget instance's element
     */
    this.onCreate = function(configs) {
      // Store configs in a var at top of instance closure
      _(options = _(configs).extend({
        // And set the activeValue if there's a defaultValue
        activeValue: _(configs.values).findWhere({value: configs.defaultValue})
      }));
      // Render using template from config and options from config
      $frame = $CMPR.render(options.frameTemplate, options);
      // Then call the update to render the repeater's bound values
      valueUpdate();
      // return a reference to the DOM element
      return $frame;
    };



    /**
     * Public handle to package widget state as a query fragment object.
     * Also triggers re-render on the widget's value bindings.
     *
     * @return {Object} Like: {field:string, valueLabel:string,
     *   operator:'==', value:string}
     */
    this.state = function() {
      // Fire updater function, state called when there's a possible update
      options.valueBinding && valueUpdate();
      // Return falsey if the widget is stateless or inactive, else object:
      return !options.stateless && options.activeValue && {
        // listenTo is an alias at subscription-level for field
        field: options.listenTo,
        // Manually set the widget to show display or real value
        valueLabel: (options.labelBuilder || _.identity)(_.process(_.get('activeValue'),
          _.or(_.get('displayName'), _.get('value')))(options), options),
        // Since it's single, look for near-exact string matches
        operator: '==',
        // Duh
        value: options.activeValue.value
      };
    };


    /**
     * Public handle for injecting state into the widget
     *
     * @param {Object} pref Criteria for this widget
     *   {value:string} || {value:Array(string)}
     */
    this.onData = function(pref) {
      // If there's not a pref or a value prop
      if (!pref || !pref.value) return;
      // Otherwise loop thru repeater fragment of template, looking for...
      setValue(_($frame.find(repeaterQuery).children()).find(function(opt) {
        // An element where the term-value is the same as the pref's value
        return (function(elemValue, prefValue){
          return _.isString(prefValue) || !prefValue || !elemValue ?
            (prefValue + '').toLowerCase() === (elemValue + '').toLowerCase() :
            _.isEqual(elemValue, prefValue);
        })($(opt).data('valueObj').value,
          _.isArray(pref.value) ? pref.value[0] : pref.value);
      }));
    };

    /**
     * Runs cleanup on the widget
     */
    this.onDestroy = function() {
      // DOM-only exposure, just remove DOM element
      $frame.remove();
    };
  };
});

