/**** Module Info
{
  'name': 'Cache Warmer',
  'summary': 'Parses config source to find and append images to DOM eagerly',
  'keywords': ['cache', 'heater', 'warmer', 'image'],
  'settings': []
}
****/

define('entry/cache_heater',[], function() {
  /**
   * Black magic to asynchronously make the session cache
   *     all images in the forgeConfigs object.
   * These are context images and so forth, which we can
   *     infer are the most likely images to be added by JS.
   *
   * @param {string} data Text of config object
   */
  return function(config) {
    var _ = $CMPR._, $ = $CMPR.$;
    if (config('warmImageCache'))
      $CMPR.on('transport.data.received.first', function() {
        // Stick on bottom of docbody invisibly...
        $(document.body).append((function(cacheDiv, templ) {
          // Add all the images onto the cache, by reading config object...
          return cacheDiv.append(_.chain(JSON.stringify(config('widget'))
            // As a string and RegEx-ing for image strings in any quotes
            .match(/("|')[^"']*(jpe?g|gif|png)\1/g) || [])
            // Only need one instance of each to warm
            .uniq().map(function(imgUrl) {
              // Need $ of each template and hidden to be sure invisible
              return $CMPR.render(templ, {URI: imgUrl}).hide();
            // Break chain
            }).value());
        // Give it a hidden div and tell it how to make image tags
        })($('<div>').hide(), _.template('<img src=<%= URI %>/>')));
      });
  };
});

