/**** Module Info
{
  'name': 'Initialization Module',
  'summary': 'Loads in CSS and JS configs, runs intro housekeeping',
  'keywords': ['initialize', 'launcher', 'entry']
}
****/

define('entry/initialize',[ 'analytics/tracking', './compile.configs',
  './injection.conditions', './feature.loader'],
function(logsInit, CompileConfigs, ShouldInjectWith, AddFeaturesUsing) {
  // Return a closure so we can use promises on it,
  // use window.$CMPR.injectionConfigs for configs
  return function(LaunchWith) {
    // Aliasing libraries
    var _ = $CMPR._, $ = $CMPR.$,
      // Set up cross-function storage for vars needed in closure methods
      forgeData;

    init();

    function configTriggered() {

      // Get all the overrides to forge and apply them
      CompileConfigs(forgeData);
      (forgeData.bootstrap || _.identity)
        .call($CMPR, $CMPR.$, $CMPR._, forgeData);
      $('head').append($('<style>').attr({
        type: 'text/css'
      }).html(forgeData.cssFile));

      // Immediately set up logging
      logsInit(exposedConfigs);

      $CMPR.on('FATAL', function() {
        (forgeData.revert || _.identity)
          .call($CMPR, $, _, exposedConfigs);
      });

      AddFeaturesUsing(exposedConfigs);

      // Go back and run the callback, exposing a preference getter API
      LaunchWith(exposedConfigs);
    }
    /**
     * Function executed when the context is loaded that resolves
     *   APP.success(fn) callback
     *
     * @return {this}
     */
    function init() {
      // The data recieved is used as a JS config object
      forgeData = window.$CMPRCONFIGS$;
      
      // Conditionally call the injections and other config compilations.
      if (ShouldInjectWith(forgeData))
        configTriggered();
    }

    // Method to dig into the forgeData safely
    function exposedConfigs() {
      // Using safe getter with all the arguments as the get path on forge data
      return _.get(_.toArray(arguments))(forgeData);
    };
  };
});

