define('entry/app.launch',['./transport', 'gridwall/gridwall.controller',
  'widgets/widget.controller'],
function(BackendConnector, GridwallController, WidgetController) {
  return function(config) {

    // Set up transport layer
    new BackendConnector(config('backend')),

    // Make gridwallController from closure, give context from forge
    new GridwallController(config('gridwall')),

    // Make widgetController from closure, give context from forge
    new WidgetController(config('widget'));

    $CMPR.push('app.launch', config);
  };
});

