/**** Module Info
{
  'name': 'Transport Interface',
  'summary': 'The module used to talk to Sora (display backend).',
  'keywords': ['transport', 'elasticsearch', 'es'],
  'settings': [
    {
      'name': 'Database Index',
      'path': ['transport', 'index'],
      'summary': 'Database index to search against. Typically client name.',
      'description': 'The base path in the database to search within. Check \
      the database to see what the index name for your client is. Typically, \
      it is a lowercase string with no spaces',
      'input': 'text',
      'validation': 'string'
    },
    {
      'name': 'Databse Table',
      'path': ['transport', 'table'],
      'summary': 'Database table/mapping to use. Typically category number.',
      'description': 'The mapping/table to search within the index (see \
      Database Index). This normally corresponds to the highest level \
      at which products have shared attributes, and therefore a category or \
      class of products. If omitted, the app will search against ALL of the \
      products in an index (which is usually a full client product set).',
      'input': 'text',
      'validation': 'string',
      'optional': true
    },
    {
      'name': 'Query Returned Actions',
      'path': ['transport', 'targets'],
      'summary': 'Callbacks to send Sora data to',
      'input': 'textarea',
      'array': true,
      'optional': true,
      'validation': 'function'
    },
    {
      'name': 'Query Throttle Duration',
      'path': ['transport', 'throttle'],
      'summary': 'Millisecond duration setting minimum window between queries',
      'description': 'This setting defaults to 100 milliseconds and cannot be \
      any shorter. Occasionally it may be useful to lengthen the time window. \
      This setting will be ignored if it is below set to below 100 ms.',
      'optional': true,
      'input': 'text',
      'validation': 'number'
    }
  ]
}
****/

define('entry/transport',[], function() {
  return function(configs) {
    // Alias the libraries/functions we are going to use into this closure
    var $ = $CMPR.$, _ = $CMPR._, stringify = JSON.stringify,
      // Alias for better minification
      pushEvent = $CMPR.push,
      // Make a FK for tracking query order and merging updates when they come
      queryIdGen = 1,
      // Auto-increment simulator for query IDs
      lastTargetPush = queryIdGen++,
      // Number of times we allow failure before giving up on a query
      triesLeft = +(configs.retries || 0),
      // Where to send off requests
      soraUrl = _(['/', configs.url,
        configs.client, configs.category]).compact().join('/'),
      staticFilters = configs.filters || [],

      // Set up a version of query above that is debounced to prevent flooding
      debouncedQuery = _.debounce(query, configs.throttle || 100);

      $CMPR.on('initial.query.ready', function(body) {
        body && query(body);
        _.defer($CMPR.on, 'widgetController.hasUpdate', debouncedQuery);
      });
    /**
     * Takes an array of preference objects for normal queries or manual query
     * strings for initializing requests and XHRs them to the SORA backend for
     * mapping. When they come back from sora, we publish them into the targets
     * configured. NOT public, we expose this through a normal channel
     * (debounced) and expedite below.
     *
     * @param {object} queryObject Has at least a body property.
     */
    function query(queryObject) {
      // Setup of closure storage and FK order for request
      var percentMatchScorers, memo, queryId = queryIdGen++, routedRequest = {};
      // If XOR is falsey, using these conditions:
      if (!$CMPR.running ^ queryObject.initializing)
        return;
      
        
        // Run an AJAX request to SORA to get some fresh hits on cache miss
        routedRequest[queryObject.initializing ? 'select' : 'query'] = queryObject.body;

        if (_.isArray(routedRequest.query))
          routedRequest.query = queryObject.body.concat(staticFilters);
        // First attempt
        function hitSora(){
          $.ajax({
            // Use the endpoint base and delegate route based on query type
            url: soraUrl,
            // Need to post a String and not an object
            data: stringify(routedRequest),
            // POST because of large content length, basically a GET (idempotent)
            type: 'POST',
            // Request as JSON instead of text so Node knows how to handle
            dataType: 'json',
            contentType: 'application/json; charset=utf-8',
            // When we get stuff back form SORA...
            success: function(soraResponse) {
              var soraData = soraResponse.body || soraResponse,
                searchSpaceSize = soraData.hits.total,
                currentResultSetSize = soraData.hits.hits.length;

              if (soraData.hits.hits)
                _(soraData.hits.hits).each(function(hit, index) {
                  if (hit._score == null)
                    hit._score = currentResultSetSize - index;
                });


              // Debt here, need to use non-global pushes instead of swap space
              $CMPR.tableSize = $CMPR.running ?
                Math.max(searchSpaceSize || 0, $CMPR.tableSize) : 0;
              $CMPR.hitSize = searchSpaceSize;
              $CMPR.currentSize = currentResultSetSize;


              // Notify that sizes are updated
              pushEvent('table.size', $CMPR.tableSize);
              pushEvent('transport.setFoundWith', queryObject.body);
              triesLeft = +(configs.retries || 0);


              // Unpack the match % with array built above,
              //   store in percentageMatch property on hit sources
              pushEvent('transport.data.mixin', {
                data: soraData,
                prefs: _.isArray(queryObject.body) ? queryObject.body : []
              });


              // Needs to happen after matches run
              if(!$CMPR.running){
                $CMPR.running = true;
                return pushEvent('transport.data.received.first', soraData);
              }

              // This is the condition that indicates if request came in-order
              (queryId > lastTargetPush && (lastTargetPush = queryId)) ?
                // Then push the information to all of our targets
                pushEvent('transport.data.received', soraData) :
                // Condition is missed, need to tell the analytics engine about it
                $CMPR.emit('WARN.transport.data.raceCondition', queryObject);
            },
            // Use prebuilt channel to indicate a non-200 responseStatus header.
            error: function(errorResponse) {
              triesLeft > 0 ? hitSora(triesLeft--) :
                $CMPR.emit(($CMPR.running ? 'ERROR' : 'FATAL') +
                  '.transport.network', errorResponse);
            }
          });
        }
        hitSora();
    };
  };
});

