/**** Module Info
{
  'name': 'Button Widget Module',
  'summary': 'Button type widget used to run single callback actions',
  'keywords': ['button', 'widget', 'save preferences', 'start over', 'reset'],
  'settings': [
    {
      'name': 'Host Element to Inject Into',
      'path': ['widget', 'fieldConfigs', '~Name of Field~', 'container'],
      'summary': 'The container in which to insert the button',
      'description': 'This can be any element on the page, it does not need to \
      be within the querybuilder area nor gridwall.',
      'input': 'text',
      'validation': 'selector'
    },
    {
      'name': 'Prepend versus append flag',
      'path': ['widget', 'fieldConfigs', '~Name of Field~', 'prepend'],
      'summary': 'Fine-tune whether to prepend or append to containing element',
      'description': 'Setting this flag to a truthy value will make the \
      element inject as the first child of the containing element at that \
      point in time. Omitting this field or setting it to a falsey value \
      causes the default behavior, where element is simply appended to parent',
      'input':'bool',
      'optional': true
    },
    {
      'name': 'Button Appearance Template (Global)',
      'path': ['widget', 'typeConfigs', 'button', 'frameTemplate'],
      'summary': 'Underscore/ERB template for what defualt buttons look like',
      'description': 'THIS MAY ALSO BE SET ON THE \
      fieldConfigs.fieldName.frameTemplate path, which will OVERRIDE the \
      type-level definition. Just a template that gets passed the \
      rest of these configs as renderable context.',
      'input': 'textarea',
      'validation': 'template'
    },
    {
      'name': 'Button Appearance Template (By Field)',
      'path': ['widget', 'fieldConfigs', '~Name of Field~', 'frameTemplate'],
      'summary': 'Underscore/ERB template set on a single type of button',
      'description': 'This will override the setting in "Button Appearance \
      Template (Global)". The context passed to the template is the options \
      object used when instantiating the wiget after resolving hierarchies.',
      'input': 'textarea',
      'validation': 'template',
      'optional': true
    },
    {
      'name': 'Button Click Callback Action',
      'path': ['widget', 'fieldConfigs', '~Name of Field~', 'action'],
      'summary': 'The action to execute when clicking the button',
      'description': 'There are a handful of built-in functions. You may \
      pick from one of these by simply selecting the action logic, \
      or define a totally custom one by entering a callback function \
      manually.',
      'input': 'select || textarea',
      'validation': 'null || function',
      'values': ['resetApp', 'save']
    }
  ]
}
****/


define('widgets/button.widget',[], function() {
  // Pass reference to widgetController interface
  return function() {
    // These are never added to uiLogic module wrapper
    this.customInjection = true;
    // Alias libs, setup storage for shared closure variables
    var _ = $CMPR._, $ = $CMPR.$, options, $frame, self = this,

    /**
     * Factory function for included compare events
     *
     * @param {string} type Kind of functionality
     * @return {function} Execute on button click OR undefined
     */
    actionTypes = function(type) {
      return {
        /**
         * Function to clear criteria states and URL
         */
        resetApp: function() {
          // Log with the preference state at reset
          $CMPR.emit('reset button clicked',
            $CMPR.getPreferenceState().value());
          // Set the state to have no preferences at all
          $CMPR.emit('widgetController.forcedState', []);
          // Then update the UI
          $CMPR.emit('widgetController.creation', options.resetWith || []);
        },
        /**
         * Function to save criteria states to cookie
         */
        save: function() {
          var state = $CMPR.getPreferenceState().value();
          // Concat the existing prefs with the current state
          $CMPR.savePrefs($CMPR.savedPrefs().concat({
            // The pref's name is like: 'dayOfWeek month dayInMonth'
            displayName: new Date().toString().split(' ').slice(0, 3).join(' '),
            // Shrinks the state for cookie storage
            value: $CMPR.encodeState(state)
          }));
          // Notify of update
          $CMPR.emit('widget.stateChange');
          // log that the pref was saved, giving state as data
          $CMPR.emit('Save button clicked', state);
        }
      // Return the function based on passed type, or undefined
      }[type];
    };
    /**
     * Function for building DOM element and actions for widget
     *
     * @param {Object} Config object
     *   {string, function} action, {string} frameTemplate, {string} selector
     * @return {jQuery} object
     */
    this.onCreate = function(configs) {
      // Store in options which is accessible thru whole button closure
      options = configs;
      // Get action from lib or assume raw function in config
      options.action = actionTypes(options.action) || options.action;
      // Store frame in outer scope closure, rendering with template and context
      $frame = $CMPR.render(options.frameTemplate, options)
        .on('click', function() {
          // Pass some context
          options.action.call(self, options);
        });
      // Prepend or append the rendered frame, default to append
      $(options.container)[options.prepend ? 'prepend' : 'append']($frame);
      // Return reference to the element
      return $frame;
    };

    /**
     * Function cleaning the widget from DOM and memory
     */
    this.onDestroy = function() {
      // No outside references, simply remove element to GC
      $frame.remove();
    };
  };
});

