/**** Module Info
{
  'name': 'Interaction Tracking Setup',
  'summary': 'Begins tracking lots of mouse events and starts logger module',
  'keywords': ['log', 'logger', 'loggins', 'tracking', 'analytics', 'events']
}
****/


define('analytics/tracking',['analytics/logger'], function(Logger) {
  /**
   * Uses Logger dependency to make log api using window.$CMPR as context
   */
  return function(getConfig) {
    // Namespace libraries
    var _ = $CMPR._, $ = $CMPR.$,
    // Make a new logger using clean object as starting event store
    logger = new Logger(getConfig('logging'));

    // Go and grab the configs
    logger.log('A/B Group ConfigURL')(window.$CMPRCONFIGURL$);
    logger.log('Default Order-By')(getConfig('widget', 'fieldConfigs',
      'sort.axis', 'defaultValue') || 'No Order-By');

    // Immediately log that we began the session
    logger.log('Session Begun')();

    $CMPR.on('FATAL', logger.log('FATAL'));
    $CMPR.on('ERROR', logger.log('ERROR'));
    $CMPR.on('WARN', logger.log('WARN'));

    // Attempt to log states when user leaves
    $(window).bind('beforeunload', function() {
      // With appropriate channel name and using API above to get entire state
        logger.log('Criteria on Page Leave')(
          $CMPR.getPreferenceState().value());
    });

    // Make the system log when a person scrolls, debounced in scroll channel
    $(window).on('scroll resize', _.debounce(logger.log('Scrolls', function() {
      // Store top and height for caluculations,
      var top = $(this).scrollTop(),
        // ... where this refers to window
        height = $(this).height();
      // Data sent to 'Scroll' channel has top, bottom and height
      return {top: top, height: height, bottom: top + height};
    // Debounced rate throttle in milliseconds to prevent overuse
    }), 250));

    // Begin logging all clicks into 'Clicks' channel w/ decorator
    $(window).on('click', logger.log('Clicks', function(ev) {
      // Sending channel only the coordinates relative to page
      return {x: ev.pageX, y: ev.pageY};
    }));

    // Pass start time at execution in as beginning point of load interval
    (function(startTime) {
      // When the DOM is done painting and ready for use
      $(document).ready(function() {
        // Log the delta time b/w begin and end
        logger.log('DOM Load Time')(new Date().getTime() - startTime);
      });
    // The time immediately at setting interval log up
    })(new Date().getTime());

    // Send off user agent string immediately
    logger.log('User Agent String')(navigator.userAgent);

    // Extend the $CMPR object to have logger API
    $CMPR.expose({
      // Aliased function that makes
      log: logger.log,
      // API for getting the full log set or a named channel
      logs: function(eventName) {
        // Alias events to object grouped by channel
        return (function(events) {
          // If there's an eventName, return that array subset, else object
          return eventName ? events[eventName] : events;
        // Build object using logs array and making keys channel names
        })(_(logger.eventStorage).groupBy('event'));
      }
    });

    $CMPR.on('scenario', logger.log('Interacted with scenario'));
    $CMPR.on('widget.hasUpdate', _.once(logger.log('Interacted with Compare')));

    /*
    // Begin tracking mouse position, debounced, w/ decorator
    $(window).on('mousemove', _.debounce(logger.log('Mouse Position',
      // Send mouse coords relative to page
      function(ev) { return {x: ev.pageX, y: ev.pageY}; }, 250)));
    */
  };
});

