/**** Module Info
{
  'name': 'URL Deep Linker',
  'summary': 'Used saving and decoding state in the GET url hash',
  'keywords': ['URL', 'state', 'back button']
}
****/


/**
 * URL state representation add-in.
 * Upon initialization, this file attempts to read the state we
 *   should restore widgets to, by reading the URL's hash and checking
 *   for a compressed JSON object wraped in $CMPR$ tags.
 * Also, this module modifies the widgetController.update function,
 *   by intercepting the value passed into the function and
 *   encoding it into the URL for later use.
 * When this interception is done, the widgetController.update runs as it
 *   normally does, with the same, unmodified data passed along non-intrusively.
 */
define('entry/deeplinking',[], function() {
  // Reference to API to allow restores
  return function(configs) {
    // Aliasing library for ease of use
    var _ = $CMPR._, encloser = /(\$CMPR\$).*\1/, 

    linkedState = _(location.hash).run(
      // Grab the first match to the enclosing tags
      _.match(encloser), _.first,
      _.condition(_.pass, _.process(
      // Kill the enclosing tags and only take the insides
        _.replace(/\$CMPR\$/g),
        // Unescape the characters
        decodeURIComponent,
        $CMPR.decodeState
      ))
    );

    if (!configs('deeplinking'))
      return;

    $CMPR.on('widgetController.hasUpdate', _.condition(_.get('silent'), _.pass, _.process(
      _.get('body'),
      // encoder above
      $CMPR.encodeState,
      // escape unfriendly chars to inject into URL
      encodeURIComponent,
      // Enclose in our special delimiter tags
      _.enclose('$CMPR$'),
      // Take the string and put it at end of cleaned URL
      function(str) {
        location.hash = location.hash.replace(encloser, '') + str;
      }
    )));

    $CMPR.on('app.launch', function(){
      if (linkedState && _(linkedState).any(function(st) {
        return _(['compare', 'display', 'product']).any(function(matter) {
          return ~st.field.indexOf(matter);
        });
      })){
        $CMPR.push('initial.query.ready');
        $CMPR.push('widgetController.forcedState', linkedState);
        $CMPR.running = true;
        _.defer($CMPR.emit, 'widget.stateChange');
      }
    });
  };
});

