define('widgets/meta.template',[], function() {
  return function() {
    var $ = $CMPR.$, _ = $CMPR._, $rootScope, $frame = $('<div></div>'),
      $$directives = [], $$rawDirectives, cleaningPool = [];

    $CMPR.expose({
      addDirectives: function(directiveObject) {
        $$directives = $$directives.concat(_.map(directiveObject, function(directive, name) {
          return {
            signature: 'data-cmpr-' + name,
            query: '[data-cmpr-' + name + ']',
            uses: directive
          };
        }));
      }
    });

    $CMPR.addDirectives($$rawDirectives = {
      repeat: function($element, $signature, $scope) {
        var $$storage = {
          parent: $element.removeAttr('data-cmpr-repeat').parent(),
          clone: $element.clone()
        };
        $element.remove();
        with($scope)
          _(eval($signature)).each(function(subscope) {
            $$storage.parent.append($$activate($$storage.clone.clone(), subscope));
          });
      },
      bind: function($element, $signature, $scope) {
        with($scope)
          $element.text(eval($signature));
      },
      show: function($element, $signature, $scope){
        with($scope)
          eval($signature) || $element.remove();
      },
      classes: function($element, $signature, $scope) {
        with($scope)
          $element.addClass(eval($signature));
      },
      id: function($element, $signature, $scope) {
        with($scope)
          $element.attr('id', eval($signature));
      },
      style: function($element, $signature, $scope) {
        with($scope)
          $element.attr('style', $element.attr('style') + ';' + eval($signature) + ';');
      },
      event: function($element, $signature, $scope) {
        var $$storage = $signature.split('::');
        with($scope)
          $element.on($$storage[0], function($event) {
            eval($$storage[1]);
            $$apply();
            $CMPR.push('widget.stateChange');
          });
      },
      click: function($element, $signature, $scope) {
        $$rawDirectives.event($element, 'click::' + $signature, $scope);
      },
      resize: function($element, $signature, $scope) {
        // oops
      }
    });

    function $$apply() {
      return $frame.empty().append($$render($rootScope.frame, $rootScope));
    }

    function $$render(template, $scope) {
      return $$activate($CMPR.render(template,
        _.extend({}, $scope, {$scope: $scope, $rootScope: $rootScope})), $scope);
    }

    function $$activate($rootNode, $scope) {
      _.each($$directives, function(directive) {
        if ($rootNode.attr(directive.signature)){
          directive.uses($rootNode, $rootNode.attr(directive.signature), $scope);
          $rootNode.removeAttr(directive.signature);
        }
        _.each($rootNode.find(directive.query), _.process($, function($node) {
            directive.uses($node, $node.attr(directive.signature), $scope);
            $node.removeAttr(directive.signature);
          }));
      });
      return $rootNode;
    }

    this.onCreate = function(configs) {
      $rootScope = configs;
      if ($rootScope.stateInjector)
        this.onData = function(state) {
          $rootScope.stateInjector(state, $rootScope, $, _);
          $$apply();
        };
      if ($rootScope.stateReader)
        this.state = function() {
          return $rootScope.stateReader($rootScope, $, _);
        };
      $frame.attr('id', 'cmpr_custom_' + $rootScope.signature + '_widget');
      return $$apply();
    };

    this.onDestroy = function() {
      $frame.remove();
    };
  };
});
