/**** Module Info
{
  'name': 'Widget Controller',
  'summary': 'Handles all criteria state synchronizations and queries',
  'settings': [
    {
      'name': 'Primary Widget Toolbar',
      'path': ['widget', 'container'],
      'summary': 'Where all of the main elements for the query builder go',
      'description': 'Any widget without customInjection or a secondary \
      flag will go here, can be an element of any shape or size.',
      'input': 'text',
      'validation': 'selector'
    },
    {
      'name': 'Secondary Widget Container',
      'path': ['widget', 'secondaryContainer'],
      'summary': 'Where auxiliary controls go, perhaps sort order etc',
      'description': 'Any widget with a secondary flag and no customInjection \
      flag go here. Often used for sorters and pagination controls. This \
      element does not have to be anywhere near or similar to the primary one. \
      This field can be omitted, and if it is, everything goes to primary.',
      'input': 'text',
      'validation': 'selector',
      'optional': true
    }
  ]
}
****/


define('widgets/widget.controller',['./button.widget', './continuumslider.widget',
  './multiselect.widget', './rangeslider.widget',
  './singleselect.widget', './typeahead.widget',
  './pagination.widget', './meta.template'
  //,'./nlp.widget'
  ],
function() {
  var widgetAliases = ['button', 'continuumslider', 'multiselect', 'rangeslider',
      'singleselect', 'typeahead', 'pagination', 'custom'
      //, 'nlp'
    ],
    widgetModules = [].slice.call(arguments, 0);
  return function(options) {
    var _ = $CMPR._, $ = $CMPR.$,
      // Setting up other storage for internal use
      widgets = [], widgetTypes = {}, foreignKeyGen = 1,
      // Composite function for flagging id a widget is destroyable or not.
      destroyable = _.and(_.not(_.get('persistent')),
        _.process(_.get('onDestroy'), _.isFunction));

      // Go back and actually execute the init now that methods are defined.
      init();

      /**
       * Scrapes the full state from all of the widget at runtime.
       * This scraping model means that state tracking is distributed,
       *   ensuring each widget can track state in a manner most suited for it.
       */
      function getState() {
        // Grab all tracked widgets in controller
        return _.chain(widgets)
          // Filter them by if there's even an accessible state function
          .filter(_.compose(_.isFunction, _.get('state')))
          // On each one, produce decorated state array
          .map(function(widg) {
            // By getting the state, returning nothing if not there, adding FK
            return (_.and(_.pass, _.extendWith({
              $$foreignKey: widg.$$foreignKey
            })))(widg.state());
          // Flatten the nested array of widget states and remove missing ones.
          }).flatten().compact();
      }

      function tooManyWidgets() {
        return _($CMPR.activeFields()).filter(function(field) {
          return $CMPR._(["display", "compare", "product"])
            .any(function(whitelist) {
              return ~field.indexOf(whitelist);
          });
        }).length >= (options.maxActive || Infinity)
      }


      /**
       * Function invoked immediately after all the methods are declared.
       * This function is used for error checking on the element that is defined
       *   for targeting by default for widget injection.
       * It also executes any mixing passed in to the widgetController,
       *     passing a reference to the controller itself as the sole parameter.
       * Finally, it calls the asynchronous import
       *   statement on the default widget set.
       * This also publicly exposes the state function for external use.
       */
      function init() {
        // Allowing getting of state in any closures
        $CMPR.expose({
          getPreferenceState: getState,
          activeFields: function() {
            return _.chain(widgets).pluck('listenTo').uniq().compact().value();
          }
        });
        $CMPR.on('widget.stateChange', update);
        $CMPR.on('widgetController.forcedState', function(arg) {
          setState(arg.prefs || arg, arg.silent);
        });
        $CMPR.on('widgetController.creation', create);
        $CMPR.on('widgetController.destruction', destroy);
        _.each(widgetAliases, function(alias, ii) {
          widgetTypes[alias] = widgetModules[ii];
        });
        create(options.defaultFields || []);
      }

    /**
     * Function for creating new widgets on the page.
     * Pass in either the name of the attribute that you want to set, or a
     *   more complete payload of options to make a one-off configuration.
     * If you pass only the string in, the controller looks for the default
     *   settings that are already associated with that field.
     * If you try to invoke a widget creation for a widget that uses a template
     *   which which has not yet been defined, the widget creation action is
     *   enqueued and waits to be invoked until the template is defined.
     * This function also invokes the WidgetFactory.decorate function on
     *   the widget closure after it is invoked with new, but before
     *   it is created on the page.
     * This function also injects a uniquely identifiable $$foreignKey property
     *   on the widget. You may support the automatic state update notification
     *   that comes after creation by passing in a second
     *   truthy parameter to the function.
     */
    function create(input, suppressUpdate) {
      var coercedPayload, payload, internals;

      if (tooManyWidgets())
        return $CMPR.push('widgetController.maxWidgets', {
          widgets: widgets,
          controller: options
        });

      _([].concat(input || [])).each(function (single){
        // If the payload doesn't have a listenTo, coerce
        _.isString(single.listenTo) || (coercedPayload = {listenTo: single});

        payload = coercedPayload || single;
        // Extend the payload with anything defined in the config object defaults
        if (!payload || !payload.listenTo || !options.fieldConfigs[payload.listenTo])
          return true;
        payload = _(payload).extend(_(options)
          .omit('defaultFields', 'typeConfigs', 'fieldConfigs'),
          options.typeConfigs[
            options.fieldConfigs[payload.listenTo].template
          ] || {},
          options.fieldConfigs[payload.listenTo] || {});
        // Add a property full context having full options.fieldConfigs object
        _(payload).extend({fullContext: options.fieldConfigs});
        // Run a function passing a new instance of widget in as... 'widget'...
        (function(widget) {
          _.extend(widget, {
            // FK for preventing state update loops later
            $$foreignKey: foreignKeyGen++,
            // Public access to destroyability
            persistent: payload.persistent,
            // Allowing overrides of listener type for widget
            listenTo: payload.listenTo || widget.listenTo || ''
          // Finally make the payload the final say for any properties.
          });

          internals = widget.onCreate(payload);
          if (!payload.customInjection)
            $CMPR.push('widget.mixins', {
              widget: widget,
              internals: internals,
              payload: payload
            });
          else
            widget.$$element = internals;
          // On the runtime-defined container,
          $(options.containers[payload.container  || 'main']
            || payload.container)[payload.prepend ?
            'prepend' : 'append'](widget.$$element);
          payload.after && payload.after(widget.$$element, payload, $, _);
          // Begin tracking on page
          widgets.push(widget);
        // Passing the new closure instance for the widget
        })(new widgetTypes[payload.template]());
      });

      suppressUpdate || update();
    }


    /**
     * Call to attempt destruction of a widget.
     * Takes a reference to the widget to be removed,
     *   or the string of the widget field name.
     * Will ignore any widgets without an onDestroy method
     *   or that have a 'persistent' flag.
     * Also automatically notifies the targets of controller of
     *   the state after destruction.
     *
     * @param {string} destroyable Widget's field name/listenTo property
     */
    function destroy(destroyable) {
      // Declare variable for UI around widget frame and widget index in storage
      var decoration, ii;
      // Function works on widgets or names of their fields, case for string
      if (_.isString(destroyable))
        // Find the widget associated with the string field name
        destroyable = _(widgets).findWhere({listenTo: destroyable});
      // Fallback to undefined if there is not $$element generated
      decoration = (destroyable || {}).$$element;
      // Find the index for the widget to destroy
      ii = _(widgets).indexOf(destroyable);
      // if ii === -1...
      if (!~ii)
        return;
      // Otherwise splice the widget at that index out and try destroying it
      (widgets.splice(ii, 1)[0].onDestroy || _.identity)();
      // If there is decoration, clip the node out
      decoration && decoration.remove();
      // And then return the updated state for chaining
      update();
    }


    /**
     * When invoked with no parameters, is a passive signal to come by and
     *   scrape the widget states. Mostly used in widgets to trigger changes.
     * When invoked with an array for the first parameter, it acts as a PUT
     *   function into the app's state, adding the prefs to the query.
     * If there is no or a falsy second parameter, PUT applies to
     *   the creation of DOM elements. This is often how widgets
     *   or mixins affect other widgets.
     * If the second parameter is truthy, the PUT operation is on the target
     *   passthroughs only - the widget states are not updated.
     *   This is most often used for something like hiding MLT states from
     *   showing 30 updated field on the page all at once.
     *
     * @param [ {Object} pref to inject new state]
     */
    function update(input, invisibly) {
      // Assign state to either the stuff coming in or the normal state scrape
      var state = input ? _.chain([].concat(input)) : getState();
      // If there's input and not an invisible flag
      if (input && !invisibly)
        // On each of the states...
        state.each(function(st) {
          // If there is not a widget with that field...
          if (!_(widgets).findWhere({listenTo: st.field || st}))
            // Inject it in with the proper listenTo property
            create(_.isObject(st) ?
              _.extend({}, st, {listenTo: st.field || st}) : st);
        });
      // Begin state update notification process.
      _.chain(widgets)
        // Ensure widgets have an onData property and that they're functions
        .filter(_.process(_.get('onData'), _.isFunction))
        // On each meeting test, push acceptable state fragments in
        .each(function(widget) {
          // Filter down to acceptable states by...
          state.filter(function(pref) {
            // ... if the state fragment doesn't come from the widget itself...
            return widget.$$foreignKey !== pref.$$foreignKey &&
              // And if the widget is subscribing to that pref channel
              (widget.listenTo === '*' || widget.listenTo === pref.field);
          // Finally push each state fragment meeting condition into the widget
          }).each(widget.onData);
        });
      // Special case to monitor present widgets, hit that subset with:
      _.chain(widgets).where({listenTo: 'FIELDS'}).invoke('onData', {
        // The unique set of widget fields
        value: $CMPR.activeFields()
      });
      $CMPR.push('widgetController.hasUpdate', {
        body: state.reject(_.get('suppressState')).value(),
        silent: invisibly
      });
    }


    /**
     * Same as update function but clears all existing state
     * This will completely kill anything in the DOM that's not within
     *     the preference set passed in, unless widgets are not destroyable.
     * You can also set the state invisibly by passing the second parameter
     *   as falsy, which just totally overrides the old state programmatically,
     *   without affecting the UI. This is useful in MLT situations, where you
     *   want to set the state for only one query, and then have it snap back
     *   to what the user set before when dismissed.
     */
    function setState(preferences, invisibly) {
      if (invisibly) // Truncate call and avoid destruction of states (MLT etc)
        return update(preferences, invisibly);
      // Remove anything not flagged as persistent (defined above)
      _.chain(widgets).filter(destroyable).invoke('onDestroy');
      // Leaving state only for persistent
      widgets = _(widgets).reject(destroyable);
      // Send flush event to widgets that support it
      _.chain(widgets)
        .filter(_.process(_.get('onData'), _.isFunction))
        .invoke('onData', {flush: true});
      // Emit the state again to targets
      update(preferences);
    }
  };
});

