/**** Module Info
{
  'name': 'Initial State Attacher (Patch Zero)',
  'summary': 'Scans client rendered page and pairs elements to Compare items',
  'keywords': ['patch zero', 'attachment', 'initialize', 'gridwall']
}
****/

define('gridwall/patchzero',[], function() {
  // Should be a reference to a storage array for elements
  return function(storage) {
    // Alias libraries we need in the module
    var _ = $CMPR._, $ = $CMPR.$,
      // Set up some storage for cross-module objects
      queryFrags, buildHitId, order = {}, props, attached;

    /**
     * Binds an external set of elements to a container and adds data
     *
     * @param {Array} onPageItems Of jQuery nodes
     * @return {this} For chaining
     */
    this.to = function(onPageItems) {
      // On all of the incoming elements,
      _.each(onPageItems, function(sol) {
        // Set the score low so they don't show
        $(sol).attr('data-es-score', '-1000');
        // Push reference into storage so we can bind to them later
        storage.push({element: $(sol), data: {}});
      });
      // Logging the original incoming elements
      $CMPR.log('Items on Page')({number: onPageItems.length});
      // So we can chain
      return this;
    };


/*
  return [prop.replace('_source.', ''),
  // Syntax for hard matching a string to the field
  ':\"', escaped(foundValue), '\"'].join('');
*/

    /**
     * Scrapes a page using a set of scanners that seek data on solutions
     *
     * @param {Object} scanners Object of field=>callback scrapers
     * @return {this} For chaining
     */
    this.using = function(scanners) {
      // The properties to check against
      props = _(scanners).keys();
      // ES query string that will find items to attach to
      queryFrags = _(storage).map(function(sol, idx) {
        // Storage per-solution of found values
        var itemVals = [],
        // Query string fragment for a single item on the page
        fragment = _(scanners).map(function(lambda, prop) {
          // The value is result of running callback on solution element
          var foundValue = lambda(sol.element);
          // Push into storage - storage used to bind later
          itemVals.push(foundValue);
          // Passing out of map
          return foundValue;
        });
        // Building up an ordering lookup for use when result comes back
        // We add the plus one to make no indexes falsey
        order[itemVals.join('___')] = idx + 1;
        // Return the single-solution query string
        return fragment;
      // Group the ANDs together so there's no ambiguity
      });

      // The in order to use the order hash, we need a way to build keys
      buildHitId = (function(getters) {
        // We have the property getters made, return a function that uses them
        return function(hit) {
          // This is in the same format as the order hash's keys
          return getters.invoke('call', null, hit).value().join('___');
        };
      // Building up the getters above based on properties of scanners
      })(_.chain(props).map(function(prop) {
        // Return a complex get function that can dig into object source path
        return _.get.apply(_, [].concat(prop.split('.')));
      }));
      // Returned for chaining and semantic nice-ness in consumer module
      return this;
    };


    /**
     * Binds a data path to use for attachment
     *
     * @param {function} callback Place to submit the built attachment query
     * @return {this} For chaining
     */
    this.thenMerge = function() {
        // Callback to execute on the hits array to reorder them
        $CMPR.on('transport.data.received.first', function(resp) {
          // Remap every hit using the order hash we built earlier
          resp.hits.hits = _.chain(resp.hits.hits).map(function(hit) {
            // Store a var for the place, as found in the order hash
            var place = order[props.map(function(prop) {
                // Dig into the hit using property path
                return _.get(prop.split('.'))(hit) || '';
              // Join into format used by order hash keys
              }).join('___')];
            // Pack into format used by sorting function below
            return {
              // Shove to bottom if it was not found in the order hash
              place: -(place || 1000),
              // Raw hit object
              hit: hit
            };
          // sorting by place and getting the hit again to strip the order prop
          }).reject(function(hit) {
            return hit.place === -1000;
          }).tap(function(hits) {
            attached = _(hits).map(function(hit) {
              return -hit.place-1;
            });
          }).sortBy('place').pluck('hit').map(function(hit, idx) {
            // Set score to the sorted index so it shows up in DOM when rendered
            hit._score = idx;
            // Return reference to the hit with only the score modified
            return hit;
          // Break chain to pass back data in the same format it is received in.
          }).value();
        });
      // Immediately invoke callback, this is a slim promise implementation
      $CMPR.push('initial.query.ready', {
        // The top-level structure all the query transport interfaces use
        body: {
          // Facet used to author request logic
          'fragments': queryFrags,
          // Properties to map against
          'props': _(props).map(_.replace(/^_source\./)),
          // Try to match result size to what's on the page
          'size': storage.length
        },
        // Special flag letting the transports know what SORA route to use
        initializing: true
      });

      $CMPR.on('gridwallController.hitInjections', _.once(function(matchedHits) {
        var interleaves = _.difference(_.range(storage.length), attached);
        _(interleaves).each(function(ii, jj) {
          storage[ii].element.attr('data-es-score', storage.length - (interleaves.length - jj + 0.5) - ii);
          storage[ii].element.addClass('cmpr-current-hit');
          matchedHits.splice(ii, 0, storage[ii]);
        });
      }));
      // Again returned for chaining
      return this;
    };
  };
});

