/**** Module Info
{
  'name': 'Multiselect Widget Type',
  'summary': 'Widget to allow several values on the same field',
  'keywords': ['multiselect', 'widget', 'query builder'],
  'settings': [
    {
      'name': 'Main Widget Element Template (Global)',
      'path': ['widget', 'typeConfigs', 'multiselect', 'frameTemplate'],
      'summary': 'Overall Underscore (ERB syntax) template for widget',
      'description': 'Make sure to include an attribute for data-cmpr-repeater \
      to inject all of the value elements into. You may also include an \
      img[data-cmpr-preview-target] to inject preview area behavior.',
      'input': 'textarea',
      'validation': 'template'
    },
    {
      'name': 'Main Widget Element Template (By Field)',
      'path': ['widget', 'fieldConfigs', '~Name of Field~', 'frameTemplate'],
      'summary': 'Overall Underscore (ERB syntax) template for setting',
      'description': 'Does the same thing as the global flag for this widget, \
      but scoped to only one field type and overrides the global option.',
      'input': 'textarea',
      'validation': 'template',
      'optional': true
    },
    {
      'name': 'Repeated Value Element Template (Global)',
      'path': ['widget', 'typeConfigs', 'multiselect', 'valueTemplate'],
      'summary': 'The template to use for repeated values in widget',
      'description': 'Template is passed an object with context that has \
      value property, and maybe displayName and exampleUrl properties.',
      'input': 'textarea',
      'validation': 'template'
    },
    {
      'name': 'Main Widget Element Template (By Field)',
      'path': ['widget', 'fieldConfigs', '~Name of Field~', 'valueTemplate'],
      'summary': 'Overall Underscore (ERB syntax) template for widget',
      'description': 'Does the same thing as the global flag for this setting, \
      but scoped to only one field type and overrides the global option.',
      'input': 'textarea',
      'validation': 'template',
      'optional': true
    },
  ]
}
****/



define('widgets/multiselect.widget',[], function() {
  // Set up constants chared by every instance (helps with minification)
  var previewQuery = 'img[data-cmpr-preview-target]',
    activeClass = 'cmpr_active_multi_select',
    inactiveClass = 'cmpr_inactive_multi_select',
    optionClass = 'cmpr_multi_select_option';
  // Pass a reference to the widgetcontroller API
  return function() {
    // Aliasing libraries
    var _ = $CMPR._, $ = $CMPR.$,
      // Setting up storage and this reference in self
      options, $frame, $repeatHost, $preview, valueUpdate, self = this,


      /**
       * Builds a jQuery object array based on passed value set,
       *   with options.valueTemplate
       *
       * @param {Array} vals Value objects:
       *   {value:string, displayName:string, exampleUrl:string=>url}
       * @return {Array} Of jQuery objects for multiselect listings
       */
      renderValues = _.mapBy(function(valueObj) {
        // Render using value object and widget options as context
        return $CMPR.render(options.valueTemplate, {
          valueObj: valueObj, options: options
        }).on('click', function() {
          // Set the value of what's clicked
          setValue($(this));
          // Then queue a state update into event loop
          $CMPR.emit('widget.stateChange');
        // Hover => show preview area image if defined in frameTemplate
        }).on('mouseenter', function() {
          // Find injection point(s) by attribute binding, change img source
          $preview.attr('src', valueObj.url || '').show();
        // Bind with the value object context
        }).data('valueObj', valueObj)
        // Add the option and inactive classes to start
        .addClass(inactiveClass).addClass(optionClass);
      }),

      injectValues = function() {
        $repeatHost.empty().append(renderValues(options.values));
      };

      function setValue(elem, cmd) {
        if (elem.hasClass(activeClass) && cmd !== 'on' || cmd === 'off')
          elem.removeClass(activeClass)
            .addClass(inactiveClass).data('isActive', false);
        else
          elem.removeClass(inactiveClass)
            .addClass(activeClass).data('isActive', true);
        // Call any custom handlers with appropriate context items
        if (!cmd)
          _(options.onSelect).run(_.fallTo([]),
            _.callAllWith(elem, self));
      }

      function init() {
        /**
         * Rebinds values if there's a function for that defined and updates DOM
         */
        valueUpdate = _.isFunction(options.valueBinding) ? function() {
          //Set internal value store to bound function result
          options.values = options.valueBinding();
          // Actually update DOM
          injectValues();
        } : _.once(injectValues);

        valueUpdate();
      };


    /**
     * Create widget jQuery node for multiselects and sets up event listeners.
     *
     * @param {Object} configs Widget configuration object
     * @return {jQuery} Element for the widget
     */
    this.onCreate = function(configs) {
      // Store configs in cross-function scoped closure variable
      options = configs;
      // Build/store frame reference using passed template and option context
      $frame = $CMPR.render(options.frameTemplate, options);
      // Make sure the preview doesn't show when you mouse out of the widget
      $preview = $frame.find(previewQuery).hide();
      // Set reference to the injection point
      $repeatHost = $frame.find('[data-cmpr-repeater]');
      // Find the preview injection point and hide from view
      $frame.on('mouseleave', function() { $preview.hide(); });
      // Run initial update to generate elements inside internals
      init.call(this);
      // Return a reference to the jQuery object for the widget's internals
      return $frame;
    };


    /**
     * State scrape function that builds a query fragment out of the widget
     *
     * @return {Object} Criteria fragment object
     */
    this.state = function() {
      // Run finder and parser on the $frame
      return (function(values) {
        // Return null if there's a stateless flag or no checked items
        return options.stateless || !values.length ? null : {
          field: options.listenTo,
          operator: 'any',
          value: values
        };
      })(_($repeatHost.find('.' + activeClass)).run(_.mapBy($),
        _.invokeWith('data', 'valueObj'), _.mapBy(_.get('value'))));
    };


    /**
     * Function to push DOM state into the widget
     *
     * @param {Object} pref Preference object or fragment
     */
    this.onData = function(pref) {
      (function(valueArray) {
        $repeatHost.children().filter(function() {
          setValue($(this), ~_(valueArray || [])
            .indexOf($(this).data('valueObj')
              .value.toLowerCase()) ? 'on' : 'off');
        });
      })(pref && pref.value && _(pref.value)
        .run(_.arrayOnly, _.mapBy(_.lowerCase)));
    };


    /**
     * Function to clean element up.
     */
    this.onDestroy = function() {
      // No leaky pieces so just trim DOM references.
      $frame.remove();
    };
  };
});

