/**** Module Info
{
  'name': 'Gridwall Controller Widget Module',
  'summary': 'Controller does patching on incoming hits and manages updates',
  'keywords': ['gridwall', 'controller', 'products', 'attachment', 'products'],
  'settings': [
    {
      'name': 'Gridwall Element',
      'path': ['gridwall', 'selector'],
      'summary': 'Element for the client gridwall',
      'description': 'All products are scraped from this element, and all \
      solutions coming from the backend are rendered, modified, rendered \
      within this element, and then animated within this element.',
      'input': 'text',
      'validation': 'selector'
    },
    {
      'name': 'Gridwall solution mixins',
      'path': ['gridwall', 'mixins'],
      'summary': 'Object with subobjects corresponding to mixins to use',
      'description': 'Mixins declarations are mapped with the key as the \
      mixin name, and an object or function as the value. The object\'s \
      form is determined by the individual mixin.',
      'input': 'text || object',
      'validation': 'function || object'
    },
    {
      'name': 'Product Element Query',
      'path': ['gridwall', 'productSelector'],
      'summary': 'A jQuery DOM selector used to find all products on page.',
      'description': 'Used to feed renders and attachers, this field declares \
      the way to scrape an array of products off of the page.',
      'input': 'text',
      'validation': 'selector'
    },
    {
      'name': 'Render Strategy',
      'path': ['gridwall', 'renderStrategy'],
      'summary': 'Pick a gridwall product rendering strategy to use.',
      'description': 'The app supports a choice of animation strategy to use \
      when showing new solutions/products on the page. This setting declares \
      which module to load and use every time a query comes back.',
      'input': 'select',
      'options': ['Isotope', 'No Animation']
    }
  ]
}
****/

// Dependencies: renderers, solution ties, attachment strategy, and mixins.
define('gridwall/gridwall.controller',[ './patchzero'],
function(Attach) {
  // Whole-module config object passed in from the forge
  return function(configs) {
    // Alias libraries in module for ease of use
    var _ = $CMPR._, $ = $CMPR.$,
      // Storage for structures used in multiple closure functions
      $gridwall, scrape, solutions = [],
      // Build a reusable multistep process to invoke all the mixins at once
      flattenAndPrep = _.process(_.get('hits', 'hits'), _.mapBy(function(hit) {
        hit._source.score = hit._score;
        hit._source.PUID = hit._id;
        return hit;
      })),
      // Multitest to check if raw HTML element or jQuery node (insertable)
      isDomInsertable = _.or(_.isElement, _.isA($));


    /**
     * Figures out what's new and old in the DOM and makes it so
     *   render functions can do the absolute minimal amount of work to
     *   show the new solutions when they come.
     *
     * @param {Object} Solution with 2 solution arrays: 'old', 'incoming'
     * @return {Object} Solution with updated score and display info and
     *    properties 'new', 'old', 'incoming' (new is the diff b/w other two)
     */
    function patch(sols) {
      // Build an index based on Compare product ID, by DOM attr
      var oldSolutionSet = _.chain(sols.old).indexBy(_.get('data', 'PUID')),
      // Index the new set by the id property (could do by attr too)
      newSolutionSet = _.chain(sols.incoming).indexBy(_.get('data', 'PUID')),
      // Grab the PUID keys off of the object fields discovered by grouping
      incomingKeys = newSolutionSet.keys().value(),
      // The new product tiles are the ones that are not in there yet
      addableIds = _.difference(incomingKeys, oldSolutionSet.keys().value()),
      // Use the IDs from to pick actual elements from new set
      addableSolutions = newSolutionSet.pick(addableIds);
      // Fire off the patch-phase mixins with partially derived diff
      $CMPR.push('gridwall.patch.mixin', {
          // This is the stuff coming from server
          incoming: newSolutionSet.value(),
          // This is the stuff from server NOT on page
          new: addableSolutions.value(),
          // This is all the stuff on page
          old: oldSolutionSet.value()
      });
      // Scrape the page for solutions to update some display properties
      _.chain(this.scrape()).map($).each(function(solTile) {
          // Change the score to reflect score based on current criteria,
          //   or zero if not in the 'incoming' set from server
          solTile.attr('data-es-score',
            // Try setting to the incoming value
            (newSolutionSet.value()[solTile.attr('data-puid')] || {
              // Otherwise mock out zero-score solution
              data: {score: '0'}
            // And set to the data.score
            }).data.score);
          // Strip everything of the visibility flag we use
          solTile.removeClass('cmpr-current-hit');
          // Then, if the score of a hit is NOT zero,
          if (solTile.attr('data-es-score') !== '0')
              // Add visibility back (so it's only hits in 'incoming' field)
              solTile.addClass('cmpr-current-hit');
      });

      return (function(newHits){
        $CMPR.push('gridwallController.hitInjections', newHits);
        // Return the object with the diff included.
        return {
          // Things that were on the page before
          old: oldSolutionSet.values().value(),
          // Things that came from server but weren't on the page
          new: newHits,
          // Things that came from the server, whether old or new
          incoming: newSolutionSet.values().value()
        };
      })(addableSolutions.values().map(function(sol) {
        // Call them current hits for display filtration
        sol.element.addClass('cmpr-current-hit');
        // Return modified original jQuery node reference
        return sol;
      // Break the chain to avoid later silliness in interfaces
      }).value());
    }


    /**
     * Initializes the gridwall by defining public scraper and attaching
     *
     * @return {this}
     */
    function init() {
      // Save gridwall in closure
      $gridwall = $(configs.selector);
      // Finally wrap in a public method
      this.scrape = function() { return $(configs.productSelector); };
      // Composing all initializer functions for execution chain
      _(this).run(configs.init || []);
      // Reference to where controller stores solutions
      new Attach(solutions)
          // Give nodes already on page
          .to(this.scrape())
          // Give scanners used to build attachment query context
          .using(configs.attachmentScanners)
          // Use SORA transport interface as the send method for the query
          .thenMerge();
      // Allow method chaining
      $CMPR.on('transport.data.received', this.update);
      return this;
    }


    /**
     * Take an array of project data, build them all
     *   apply all the mixins, pass to render function. Bound to closure.
     *
     * @param {Array} products From ES hits.hits property
     * @return {this}
     */
    this.update = _.bind(function(products) {
      var incoming = _(products).run(flattenAndPrep,
        // Built with SolutionGenerator build handle, filter out bad hits
        _.mapBy(function(data) {
          // Object built thru generating template and running mixins process
          return {
            // Render function to get jQuery element, using template and data source
            element: $CMPR.render(configs.template, data._source),
            // Change data to the actual hit source
            data: data._source
          };
        }), _.compact);
      $CMPR.push('solution.mixins', incoming);
        // Data Object coming from the patch function
        return (function(solData) {
            // Add only new solutions to Array storage for module
            solutions = solutions.concat(solData.new);
            // Run our prebuilt process for mixins at this step w/ context
            $CMPR.push('gridwall.update.mixin', {
              solutions: solData,
              gridwall: $gridwall
            });
            $CMPR.push('gridwall.prerender', solutions);
            // Render what we have using closure as context again
            solData.solutions = solutions;
            return this.render.call(this, solData);
        // Pass reference to module again and call patch
        }).call(this, patch.call(this, {
            // Incoming are the hits off the ES object,
            incoming: incoming,
            // The originals are what are already being tracked here
            old: solutions
        }));
    // Binding to gridwallController as closure
    }, this);


    /**
     * Use passed in render strategy or raw create/delete to show solutions
     *
     * @param {Object} solutionObject Contains Arrays of solution objects
     */
    this.render = $CMPR.pusher('gridwall.render.' +
      (configs.renderStrategy || 'default'));
    
    init.call(this);
  };
});

