define('entry/cookie.interface',[], function() {
  return function(configs) {
    var $ = $CMPR.$,
      scope = ['cmpr',
        configs('backend', 'client'),
        configs('backend', 'category') || ''
      ].join('');
    // Expose API for cookie scoped to this category
    $CMPR.expose({
      // Either save the cookie with a value or kill it
      savePrefs: function(val) {
        // based on if the value is passed
        return val ?
          // Set cookie to the value for the category for a year
          $.cookie(scope, JSON.stringify(val), {expires: 365}) :
          // Or nuke the cookie for the category
          $.removeCookie(scope);
      },
      // Get the namespaced cookie for this category
      savedPrefs: function() {
        // Parse it out and provide empty array for default
        return JSON.parse($.cookie(scope) || '[]');
      }
    });
  };
});

