/**** Module Info
{
  'name': 'Continuum Slider Widget Module',
  'summary': 'Single value slider that tests for proximity to some value',
  'keywords': ['continuum', 'slider', 'widget', 'simple slider'],
  'settings': [
    {
      'name': 'Continuum Appearance Template (Global)',
      'path': ['widget', 'typeConfigs', 'continuumslider', 'frameTemplate'],
      'summary': 'Overall Underscore (ERB syntax) template for widget',
      'description': 'Include attribute data-cmpr-continuum-slider \
      to inject slider functionality. You may also include an \
      img[data-cmpr-preview-target] to inject preview area behavior.',
      'input': 'textarea',
      'validation': 'template'
    },
    {
      'name': 'Continuum Appearance Template (By Field)',
      'path': ['widget', 'fieldConfigs', '~Name of Field~', 'frameTemplate'],
      'summary': 'Override template for a specific widget field',
      'description': 'Same functionality as the global version, only scoped \
      to a single field for the widget and overrides the global if defined.',
      'input': 'textarea',
      'validation': 'template',
      'optional': true
    }
  ]
}
****/


define('widgets/continuumslider.widget',[], function() {
  return function() {
    // Setup aliases to make code more terse
    var _ = $CMPR._, $ = $CMPR.$, self = this,
    // Store channel for logging that the widget was touched
    logUsed = $CMPR.log('Continuum Slider Used'),
    // Storage for some elements and functions used across module
    options, $frame, $slider, $image, smooth;
    // Triggered when the slider is moved, updates preview and state
    slideUpdater = function() {
      // Set this to prevent the default image from sticking
      options.active = true;
      // Setting the preview src to the associated URL
      $image.attr('src', options.values[smooth($slider.slider('value'))]
        // Falling back to the default one
        .url || options.defaultExampleUrl || '');
      // Push the state of the widget into the channel we have set up
      logUsed(self.state());
      // Then notify the widget controller of updates
      $CMPR.emit('widget.stateChange');
    };


    /**
     * Create a widget's DOM representation and make it usable
     *
     * @param {Object} payload Widget setup configurations
     * @return {jQuery} Node for the widget
     */
    this.onCreate = function(payload) {
      // Clone the passed payload into module storage and add some context
      options = _.extend({}, {
        // Always start in off position
        active: false,
        // Grain refers to the scaling and smoothing increment to snap to
        grain: 1000 / (payload.values.length - 1)
      // Copying the actual passed properties at last
      }, payload);
      // Setting up the smoothing function now that we have the grain
      smooth = _.memoize(_.process(
        // Simple scaling algorithm, snapping to integers
        _.multiplyBy(1 / options.grain), Math.round));
      // Render the passed template w/ context to get frame
      $frame = $CMPR.render(options.frameTemplate, options);
      // Inject the slider behavior onto anything with the appropriate attribute
      $slider = $frame.find('[data-cmpr-continuum-slider]').slider({
        // Used because this is compare minimum
        min: 0,
        // Likewise is compare max
        max: 1000,
        // Start in the middle because it's as good as anywhere
        value: 500,
        // Call the master updater we declared above when things stop moving
        change: slideUpdater,
        // Do the same DURING moves, only throttle it to prevent lagging
        slide: _.throttle(slideUpdater, 100)
      });
      // Bind the image preview src tag updates to elements with the attribute
      $image = $frame.find('[data-cmpr-preview-image]')
        // And iitialize with the default image, if there is one
        .attr('src', options.defaultExampleUrl || '');
      // Give back the reference to the built widget internals for controller
      return $frame;
    };


    /**
     * Public handle for external modules to check criteria set by widget
     *
     * @return {Object} Criteria preference fragment
     */
    this.state = function() {
      // Contingent on it being active
      return options.active ? {
        // Field is same as data subscription
        field: options.listenTo,
        // Equality since the continuum is point-in-space
        operator: '==',
        // Grab the value by smoothing the slider value and lookup in values
        value: options.values[smooth($slider.slider('value'))].value
      // Returning nothing if the widget was not active
      } : null;
    };


    /**
     * Public handle to push state into the widget's DOM representation.
     *
     * @param {Object} pref Widget criteria preference fragment
     */
    this.onData = function(pref) {
      // Aliasing out the index of the value we find by array search below
      (function(idx) {
        // If it's anything other than -1, set the slider scaled by grain value
        ~idx && $slider.slider('value', idx * options.grain);
      // indexOf + find is a custom array search trick
      })(_(options.values).indexOf(_(options.values).find(function(opt) {
        // Our comparison only cares about value property equivalence
        return opt.value.toString().toLowerCase() === ((_.isArray(pref.value) ?
          // There are several formats this could be, this translates them
          pref.value[0] : pref.value) || '').toString().toLowerCase();
      })));
    };


    /**
     * Public handle to clean DOM and JS heap of widget.
     */
    this.onDestroy = function() {
      // Simple removal because widget doesn't make leaky references.
      $frame.remove();
    };
  };
});

