define('gridwall/render/default.render',[], function() {
  return function(configs) {
    // Alias libraries we will be using
    var _ = $CMPR._, $ = $CMPR.$;

    // Fallback method for create and delete of element
    $CMPR.on('gridwall.render.default', function(solutionObject) {
      // In fallback just nuke the tracked solutions, it's what happens in DOM
      solutionObject.solutions = _.chain(solutionObject.old
          .concat(solutionObject.new))
        // Index by the unique ID so we can skim only incoming hits
        .indexBy(_.get('data', 'PUID'))
        // Skimming phase only taking the incoming hits
        .pick(_(solutionObject.incoming).map(_.get('data', 'PUID')))
        // Grab value objects from skimmed set and break chain
        .values().value();
      // Empty the gridwall out and append the incoming solutions
      $(configs('gridwall', 'selector')).empty()
        .append(_.chain(solutionObject.solutions)
        // grab (jQuery object) node from solution,  make sure it's valid
        .pluck('element').filter(_.or(_.isElement, _.isA($)))
        // And arrange by descending score
        .sortBy(_.process(_.method('attr', 'data-es-score'), _.negate))
        // Break the chain
        .value());
    });
  };
});
