/**** Module Info
{
  'name': 'Product Hover Intent Tracker',
  'summary': 'Used to log when a user hovers over solution for over 250 ms.',
  'keywords': ['hover', 'intent', 'tracking', 'solution']
}
****/


define('gridwall/solutions/hovertrack.mixin',[], function() {
  return function() {
    // We will be using jQuery in this module for event bindings
    var $ = $CMPR.$;


    /**
     * Used to attach hover timers that are sent to log to solutions
     *
     * @param {Object} solution Solution object from gridwall
     * @return {Object} Modified solution object with added hover timer
     */
    $CMPR.on('solution.mixins', $CMPR._.mapBy(function(solution) {
      // Don't attempt anything if the element is not found on the solution
      if (!solution.element) return solution;
      // Setting up storage for the two enter/exit events
      var entryCallback, exitCallback;
      // Define mouseenter triggered event
      entryCallback = function(entry) {
        // Save the current time so we can get an interval later
        var entryStamp = new Date().getTime(),
          // Saving data scope inside outer closure
          context = solution.data;
        // Mouseleave callback. No var keyword, is defined in outer closure
        exitCallback = function(exit) {
          // Simple time interval subtraction
          var duration = new Date().getTime() - entryStamp;
          // Deemed appropriate low-pass filter level for intent by design
          if (duration >= 250)
            // Finally we log that intent happened if it  was long enough
            $CMPR.log('Solution: Intent')({
              // Bound above, this is the info about hovered solution
              solution: context,
              // In milliseconds the mouse spent over the solution tile element
              duration: duration,
              // Context for a bayesian filter - what prefs led to intent?
              criteria: $CMPR.getPreferenceState().value()
            });
          // since we just finished executing event with timer, reset exit
          $(this).unbind('mouseleave', exitCallback);
        };
        // Actually bind the callback above at end of entry callback
        $(this).on('mouseleave', exitCallback);
      };
      // At the end of the module, make sure to activate the entry callback!
      solution.element.on('mouseenter', entryCallback);
      // Return reference to solution with the event bound to it.
      return solution;
    }));
  };
});

