/**** Module Info
{
  'name': 'Pagination Widget Module',
  'summary': 'Builds element for tabbing through the products in a category',
  'keywords': [],
  'settings': []
}
****/


define('widgets/pagination.widget',[], function() {
  var hits = 1, size = 1, pageNum = 1, trailing = 1, lastQuery, originalSet = [];
  return function() {
    this.customInjection = true;
    var _ = $CMPR._, $ = $CMPR.$,
      options, $frame = $('<div class="cmpr_pagination_wrapper"></div>');

      function pageTo(num) {
        trailing = pageNum;
        pageNum = num;
        $CMPR.push('widget.stateChange');
      }

      function ignorePageNum(state) {
        return _.chain(state).reject(function(st) {
          return st && st.field === 'page.number';
        }).map(function(st) {
          st.mustHave = 0 + !!st.mustHave;
          return st.mustHave;
        }).value();
      }

      function getAction(action) {
        return ({
          '<<': function() {
            pageTo(1);
          },
          '<': function() {
            pageTo(Math.max(pageNum - 1, 1))
          },
          goTo: function() {
            pageTo($(this).data('pageNum' || 1));
          },
          '>': function() {
            pageTo(Math.min(pageNum + 1, getPageCount()));
          },
          '>>': function() {
            pageTo(getPageCount());
          }
        })[action];
      };

    function dynamicTypeRender(object, pageCount, computed) {
      return $('<div class="cmpr_pagination_piece">' + (_.isFunction(object) ?
        object(pageNum, pageCount, computed) :
        _.template(object, {
          current: pageNum,
          max: pageCount,
          computed: computed
        })) + '</div>');
    }

    function getPageCount() {
      return (function(hits, size) {
        return (Math.floor(hits/size) || 0) + !!(hits%size) || 1;
      })(hits, size);
    }

    function idSet(resultItem) {
      return _(resultItem.hits.hits).pluck('_id');
    }

    function meetsCondition(pageCount) {
      return function(piece) {
        return !piece.condition || piece.condition(pageNum, pageCount);
      }
    }

    var updateBindings = function() {
      var pageCount = getPageCount();
      $frame.empty().append(_.chain(options.pieces).filter(meetsCondition(pageCount)).map(function(piece) {
        var computed = piece.compute && piece.compute(pageNum, pageCount);
        return dynamicTypeRender(piece.look, pageCount, computed).on('click', function() {
          (getAction(piece.action) || piece.action
            || _.pass)(pageNum, pageCount, pageTo, computed);
        });
      }).value());
    };

    this.onCreate = function(configs) {
      options = configs;

      $CMPR.on('widgetController.hasUpdate', function(state) {
        var sizePiece = _(state && state.body).findWhere({field: 'page.size'});
        if(sizePiece)
          size = sizePiece.value;
      });

      function displayConditions(signature) {
        originalSet = idSet(signature);
        $CMPR.on('transport.data.received', function(hitObject) {
          hits = hitObject.hits.total;
          updateBindings();
          if (_.isEqual(originalSet, idSet(hitObject))){
            $(options.clientPagination).show();
            $frame.hide();
          } else {
            $(options.clientPagination).hide();
            $frame.show();
          }
        });
      }

      $CMPR.on('transport.data.received', _.once(displayConditions));

      $CMPR.on('transport.setFoundWith', function(query) {
        if (!_.isEqual(ignorePageNum(lastQuery), ignorePageNum(query))){
          trailing = pageNum = 1;
          lastQuery = query;
        }
      });

      updateBindings();

      _.chain(options.injectInto).map($).each(function(cont) {
        cont.append($frame);
      });

      return $frame.hide().on('click', function() {
        $CMPR.emit("ANALYTICS.pagination.pageChange", {
          fromPage: trailing,
          toPage: pageNum,
          numPages: getPageCount(),
          pageSize: size,
          hitSetSize: hits
        });
      });
    };

    this.state = function() {
      return {
        field: 'page.number',
        operator: '==',
        value: pageNum
      };
    };

  };
});

