/**** Module Info
{
  'name': 'Percent Match Mixin Module',
  'summary': 'Used to show hard %s of hits in addition to score',
  'keywords': ['percent', 'match', 'percentage', 'hard match']
}
****/

define('gridwall/solutions/percentmatch.mixin',[], function() {
  return function(configs) {
    // Alias underscore lib
    var _ = $CMPR._,
      // These are widget name substrings that ES uses
      matchWhitelistSubstring = _(['compare', 'product', 'display'])
        .map(_.hasString);


      /**
       * Checks if the pref fieldname flags for a matchable field
       *
       * @param {Object} pref Preference object
       * @return {Boolean} Tells if its a matchable pref
       */
      function matchable(pref) {
        // Run all substring checks on the pref and see if any are true
        return _(matchWhitelistSubstring).run(
          _.callAllWith(pref.field), _.any);
      }


      /**
       * Builds a function that safely access the field needed
       *   on the hit's _source subobject
       *
       * @param {Object} pref Preference object
       * @return {function} Takes hit returning hit's value for the pref's field
       */
      function sourceAccessor(pref) {
        // Return a storable function
        return _.get(['_source'].concat(pref.field.split('.')));
      }


      /**
       * Takes a pref and returns a match function according to operator type
       *
       * @param {Object} pref Preference object
       * @return {function} Matcher or noop for unknown operators
       */
      function againstResolver(pref) {
        return _.process(sourceAccessor(pref), ({
          '<=': _.isLte, '>=': _.isGte, '<': _.isLt, '>': _.isGt,
          '==': function(desiredVal) {
            return function(actualVals) {
              return ~_(actualVals || []).map(_.lowerCase).indexOf(
                _.toString(_.arrayOnly(desiredVal).join('').toLowerCase()));
            };
          },
          'range': _.within,
          'any': function(desiredVals) {
            return function(actualVals) {
              return _.intersection(desiredVals, actualVals).length;
            };
          }
        }[pref.operator] || _.noop)(pref.value) || _.noop);
      }


    
    /**
     * Builds set of matchers to compare against hit set once it comes in
     *
     * @param {Object} preference
     * @return {Array} Matchers that return if a hit matches prefs for query
     */
    var matchFunctions = _.process(_.filterBy(matchable),
      _.mapBy(againstResolver), _.compact);


    /**
     * Unpack a set of hits, give them all a matchPercentage based on matchers
     *
     * @param {Object} data Raw elaticsearch hit, {Array} matchers
     */
    function calculate(data, prefs) {
      var matchers = matchFunctions(prefs);
      // Dig into hits piece of return object, run for each hit
      _(data.hits.hits).each(function(hit) {
        // Aliasing straight to total and matched as raw numbers
        (function(total, matched) {
          // Mutate _source to have the field we need, either 100 or actual %
          hit._source.matchPercentage = !total ?
            100 : Math.round(100 * matched / total);
        // Aliasing to numbers, length of matchers and length of met/true
        })(matchers.length || 0, _(matchers).run(
          _.callAllWith(hit), _.compact).length || 0);
      });
    };


    /**
     * Takes solutions and injects percent match into their solution elements
     *
     * @param {Object} solutions Has Array of old, new, and incoming solutions
     */
    function updateDisplay(solutions) {
      // Alias the merge between old and new for full lookup table
      (function(completeLookup) {
        // On each of the incoming solutions (that get rendered and updated)
        _(solutions.incoming).each(function(updatedInfo, updatedKey) {
          // Lookup the solution in table based on key, grab element property
          completeLookup[updatedKey].element
            // Find the subelements that have the class for injecting % match
            .find('.cmpr-percent-match-target')
            // And make the text inside the elements a string for the % match
            .text(updatedInfo.data.matchPercentage + '');
        });
      // Extend an empty object to build non-destructive total lookup table
      })(_.extend({}, solutions.old, solutions.new));
    };


    $CMPR.on('transport.data.mixin', function(pack) {
      calculate(pack.data, pack.prefs);
    });
    if (configs('percentMatch'))
      $CMPR.on('gridwall.patch.mixin', updateDisplay);

  };
});

