/**** Module Info
{
  'name': 'Typeahead Widget Module',
  'summary': 'Basic text input for pushing state into widgets',
  'keywords': ['text input', 'typeahead', 'what\'s important'],
  'settings': [
    {
      'name': 'Host Element',
      'path': ['widget', 'typeConfigs', 'typeahead', 'container'],
      'summary': 'DOM selector for the element in which to inject the widget',
      'description': 'Used by jQuery to find the an element to inject the \
      typeahead into. WARNING: if your query has more than one hit, you will \
      see more than one typeahead rendered on the page!',
      'input': 'text',
      'validation': 'selector'
    },
    {
      'name': 'Prepend versus append flag',
      'path': ['widget', 'typeConfigs', 'typeahead', 'prepend'],
      'summary': 'Fine-tune whether to prepend or append to containing element',
      'description': 'Setting this flag to a truthy value will make the \
      element inject as the first child of the containing element at that \
      point in time. Omitting this field or setting it to a falsey value \
      causes the default behavior, where element is simply appended to parent',
      'input':'bool',
      'optional': true
    },
    {
      'name': 'Frame/Overall Template',
      'path': ['widget', 'typeConfigs', 'typeahead', 'frameTemplate'],
      'summary': 'Template for the DOM node for the whole widget',
      'description': 'Underscore/ERB format template for generating the DOM \
      node associated with a typeahead. YOU MUST INCLUDE an element flagged \
      with the "data-cmpr-typeahead-hits" attribute! This will be the element \
      where all of the hits for a query fragment are injected/rendered.',
      'input': 'textarea',
      'validation': 'template'
    },
    {
      'name': 'Template for Each Hit Item',
      'path': ['widget', 'typeConfigs', 'typeahead', 'hitTemplate'],
      'summary': 'Will be used to render each met hit as a user types into box',
      'description': 'Underscore/ERB template for rendering a single hit item \
      that is found while a user types. Is passed these fields: "value", and \
      "fieldLabel".',
      'input': 'textarea',
      'validation': 'template'
    }
  ]
}
****/



define('widgets/typeahead.widget',[], function() {
  return function() {
    // Alias the libraries needed here
    var _ = $CMPR._, $ = $CMPR.$,
      // Set up cross-function closure storage
      options, $frame,
      // Process for making sure a widget's content is matchable
      fieldWhitelist = _.process(_.get('field'),
        // If any of these tokens are a substring, use it
        _.or.apply(_, _(['compare', 'display', 'product']).map(_.hasString))),
      // Make debounced log channel for when there's no hits on query
      logNoHits = _.debounce($CMPR.log('Non-matching Typeahead'), 100);
      /**
       * Finds all matching tokens from all other widgets,
       *   or spit out field names if zero-length string.
       *
       * @param {string} text Typeahead text content to parse
       * @return {Array} Of terms with any matching tokens to query string
       */
      function findHits(text) {
        // text is false when it's ', use raw fields
        return !text ? options.fieldSet :
        // Otherwise parse againt token bank to see if any are close
        _(options.termSet).filter(function(hit) {
          // By checking if anything in the lookup table for a term matches
          return _(hit.lookup).any(function(token) {
            // Defined as a case-insensitive substring of anything in lookup
            return ~token.toLowerCase().indexOf(text.toLowerCase());
          });
        });
      }
      // Shorthand for toggling visibility or querying for it
      function trayVisibility(method) {
        // If no method passed, use as getter, else setter
        return method ?
          $frame.find('.cmpr-widget-tray')[method]() :
          $frame.find('.cmpr-widget-tray').is(':visible');
      }


      /**
       * Takes an input DOM element and builds hit DOM nodes out of it,
       *   by searching the token bank agains the input's value
       * @param {jQuery} $input Typahead's input node
       * @return {Array} jQuery nodes from hit terms rendered into hitTemplate
       */
       function buildHits($input) {
        // Use the term matcher on the value of the input node
        return _(findHits($input.val())).map(function(hit) {
            // Use partially built render function using template passed
            return $CMPR.render(options.hitTemplate, hit)
              .on('click', function() {
              // Conditions for filter rather than value => open vs closed
              $CMPR.push('widget.stateChange', _.extend({}, hit, {forceOpen: true}));
              // Clear the input so user can immediately type more
              $input.val('');
              // Defer DOM actions to prevent locking
              _.defer(function() {
                // Defer tray hide again because it's low priority
                _.defer(trayVisibility, 'hide');
              });
            });
          });
      }


      /**
       * Adds lookup tables for token hits into a configuration payload
       *
       * @param {Object} payload Widget configuration object with context on it
       */
      function buildTokenLookup(payload) {
        // Make a field for only the widget attribute names
        payload.fieldSet = _(payload.fullContext).run(
          // Map the context subobjects
          _.mapBy(function(info, field) {
            // And return a readable token with NO value (field only)
            return {
              field: field,
              fieldLabel: info.label || '',
              synonyms: [],
              term: info.label,
              value: null
            };
          }),
          // Make sure only acceptable fieldnames are passed through
          _.filterBy(fieldWhitelist)
        );

        // Build a more complete lookup with values too
        payload.termSet = _(payload.fullContext).run(
          // Give all the packages fieldnames so we can discard the key
          _.mapBy(function(pkg, field) {
            // Copy keyname into a field on the value object
            return _(pkg.values || []).map(_.set('field', field));
          }),
          // Flatten them out so we don't get nesting
          _.flatten,
          // Map each of these term-token wrappers
          _.mapBy(_.process(
            // Copying the value into the term field
            _.copyFromTo('value', 'term'),
            // Generating synonyms by doing...
            _.setInternal('synonyms', _.process(
              // Making an array out of the default value and synonyms
              _.objectToArray('defaultValue', 'synonyms'),
              // Then flattening them so synonyms isn't nested
              _.flatten))),
            // Then make the term lower case to standardize
            _.modify('term', _.lowerCase),
            // Same with all the synonyms
            _.modify('synonyms', _.mapBy(_.lowerCase)
          )),
          // Kill anything that's falsey, not a string, or contains a '.'
          _.filterBy(_.process(
            _.get('term'), _.and(
              _.identity, _.isString, _.not(_.hasString('.')))
          )),
          // Try making the label the fieldname for each token
          _.mapBy(function(term) {
            // By setting the label to something in the field-only subset
            term.fieldLabel = (_(payload.fieldSet)
              // Matching based on the field propery's fieldLabel
              .find(_.equals(term, _.get('field'))) || {}).fieldLabel || '';
            // And pass back the whole object again
            return term;
          }),
          // Only take valid fields (see above)
          _.filterBy(fieldWhitelist),
          // Concat on the field subset
          _.concatWith(payload.fieldSet),
          // Then assign the lookup field to have all relvant strings
          _.mapBy(_.setInternal('lookup', _.process(
            // Parse out the properties into an array and make it flat
              _.objectToArray('synonyms', 'term', 'fieldLabel'), _.flatten)
          ))
        );
      }


      /**
       * Renders hits into the widget internals based on the input node's state
       *
       * @param {jQuery} $input DOM node for typeahead input
       */
      function renderHits($input) {
        // Store for use, building hit term DOM nodes to inject into display
        var hitsToAppend = buildHits($input);
        // Either use hits or log that there were not, passing text w/ no hits
        hitsToAppend.length || logNoHits($input.val());
        // Find the injection point, empty it, and append said hits
        $frame.find('[data-cmpr-typeahead-hits]').empty().append(hitsToAppend)
          // Find first child, then add the selected class used for arrow nav
          .children().first().addClass('cmpr-typeahead-focused-hit');
      }
      // Utility to quickly find the input node as jQuery object
      function getInput() {
        // $frame is the closure variable for the whole DOM structure
        return $frame.find('input.cmpr-criteria-typeahead-input');
      }


      /**
       * Used to move the arrow navigation class up and down on hits array
       *
       * @param {number} representing offset to move the class
       */
      function moveSelectedIndex(inc) {
        // Alias the hit nodes and make a default index
        (function(hits, current) {
          // On each hit element
          _(hits).each(function(hit, ii) {
            // If it's already got a focus class
            if (hit.hasClass('cmpr-typeahead-focused-hit')) {
              // Set it to current
              current = ii;
              // Check if we're going to go out of bounds
              if (hits[current + inc])
                // If not, remove the class from current
                hit.removeClass('cmpr-typeahead-focused-hit');
            }
          });
          // If we found a current and we're still not going out of bounds...
          if (~current && hits[inc + current])
            // Add the selection class in to the incremented current hit
            $(hits[inc + current]).addClass('cmpr-typeahead-focused-hit');
        // Map hits into jQuery objects, set default for current hit to -1
        })(_.map($frame.find('.cmpr-typeahead-hit'), $), -1);
      }


      /**
       * Adds key events to an input jQuery node...
       *   like up, down, toggle, update, submit
       *
       * @param {jQuery} node
       */
      addKeyEventsTo = _.functional('on')('keydown', function(evt) {
        // Up arrow
        if (evt.keyCode === 38)
          // Move the selection UP
          moveSelectedIndex(-1);
        // Down arrow
        else if (evt.keyCode === 40)
          // Move the selection DOWN
          moveSelectedIndex(1);
        // enter key
        else if (evt.keyCode === 13)
          // Submit a query selection
          // TECHDEBT
          $frame.find('li.cmpr-typeahead-focused-hit').click();
        // Escape key
        else if (evt.keyCode === 27)
          // hide or show the hit internals
          trayVisibility('toggle');
        // Unknown keys
        else
          // Enqueue some housekeeping actions
          _.defer(function() {
            // Render the new hits for the updates query string
            renderHits(getInput());
            // Close widgets other than this one
            $CMPR.closeAllWidgets();
            // Make sure the tray with the hits in it is showing
            trayVisibility('show');
          });
      }),


      /**
       * Add some special DOM events to the entire typeahead DOM node
       *
       * @param {jQuery} node
       */
      addClickEventTo = _.functional('on')('click', function() {
        // Make sure this is non-blocking
        _.defer(function() {
          // Figure out if we're opening or closing based on user focus
          var shouldHide = trayVisibility() && !$frame
            .find('input').is(':focus');
          // Make sure there's information to be seen in hits area
          renderHits(getInput());
          // Close any other widgets
          $CMPR.closeAllWidgets();
          // Show it unless it needs to be hidden
          trayVisibility(shouldHide ? 'hide' : 'show');
        });
      });

    /**
     * Create jQuery node and launch the typeahead based on a config payload.
     *
     * @param {Object} payload Widget configuration object
     * @return {jQuery} widget's DOM node reference
     */
    this.onCreate = function(payload) {
      // Parse all the widget context into token lookup table format
      buildTokenLookup(payload);
      // Store in cross-functional storage object above
      options = _.clone(payload);
      // Use template renderer to make $frame a jQuery object based on template
      $frame = $CMPR.render(options.frameTemplate, options);
      // Add key navigation actions to the input field
      addKeyEventsTo($frame.find('input.cmpr-criteria-typeahead-input'));
      // Add click events that close other widgets and such to the $frame
      addClickEventTo($frame);
      // Make the widget's internals hide when you click outside the widget
      $CMPR.clickOutBinding($frame.find('.cmpr-widget-tray'), $frame);
      // Make tray hidden by default
      trayVisibility('hide');
      // Put the widget's DOM node into the page
      $(options.container)[options.prepend ? 'prepend' : 'append']($frame);
      // Return a reference to the jQuery node for the widget
      return $frame;
    };
  };
});

